import {
  array, arrayOf, func, object, oneOfType, shape
} from 'prop-types';

// Import other prop types.
import navigationTypes from './navigation';
import routeTypes from './route';
import placeTypes from './place';
import locationTypes from './location';

export default {
  propTypes: {
    updatePlaces: func,
    updateLocation: func,
    updateNextPageToken: func,
    addAcceptedPlaceId: func,
    addViewedPlaceId: func,
    getSearchRadius: func,
    navigation: shape(navigationTypes.propTypes),
    route: shape(routeTypes.propTypes),
    location: shape(locationTypes.propTypes),
    places: oneOfType([
      object,
      shape({
        all: oneOfType([
          array,
          arrayOf(shape(placeTypes))
        ])
      })
    ])
  },
  defaultProps: {
    updatePlaces: () => null,
    addAcceptedPlaceId: () => null,
    addViewedPlaceId: () => null,
    places: {
      all: []
    }
  }
};
