import React, { Fragment, useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { createStackNavigator } from '@react-navigation/stack';

// Prop types.
import { navigationTypes, routeTypes } from '../types';

// Components.
import NavHeader from './NavHeader';
import AuthRoutes from './AuthRoutes';

// Screens.
import { Authentication } from '../views';

// Actions.
import { storeSocketIO } from '../actions/socketIO';
import { getUser } from '../actions/user';
import { getDates } from '../actions/dates';
import { getSelectedPlaces } from '../actions/places';
import { getNotifications } from '../actions/notifications';

// Services.
import { UserService, SocketIO } from '../services';

// Constants.
import {
  LOGIN_ROUTE, SIGN_UP_ROUTE, ROOT_ROUTE,
} from '../modules/constants/authentication';

const Stack = createStackNavigator();

function RootNavigator() {
  const [isSocketConnected, setIsSocketConnected] = useState(false);
  const storedUser = useSelector(({ user }) => user);
  const dispatch = useDispatch();

  const { uid: userId, currentDateId } = storedUser;

  useEffect(() => {
    UserService.authStateChanged()
      .then(user => {
        if (user) {
          const { uid } = user;
          dispatch(getUser());
          dispatch(getDates(uid));
          dispatch(getNotifications(uid));

          if (currentDateId) {
            dispatch(getSelectedPlaces(userId, currentDateId));
          }
        }
      });
  }, [userId]);

  useEffect(() => {
    if (!isSocketConnected) {
      const socketIO = new SocketIO();
      socketIO.start(userId);
      setIsSocketConnected(true);
      dispatch(storeSocketIO(socketIO));
    }
  }, [isSocketConnected]);

  return (
    <Stack.Navigator
      headerMode="screen"
      initialRouteName={ROOT_ROUTE}
      screenOptions={{
        header: props => <NavHeader {...props} />,
      }}
    >
      {userId ?
        (
          <Stack.Screen name={ROOT_ROUTE}>
            {props => <AuthRoutes { ...props } />}
          </Stack.Screen>
        )
        :
        (
          <Fragment>
            <Stack.Screen
              name={LOGIN_ROUTE}
              component={Authentication}
              options={{ gestureEnabled: false }}
            />
            <Stack.Screen
              name={SIGN_UP_ROUTE}
              component={Authentication}
              options={{ gestureEnabled: false }}
            />
          </Fragment>
        )
      }
    </Stack.Navigator>
  );
}

RootNavigator.propTypes = {
  ...navigationTypes.propTypes,
  ...routeTypes.propTypes,
};

export default RootNavigator;
