import React, { Fragment, useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';

// Components
import { View } from 'react-native';
import { Divider, List, Menu } from 'react-native-paper';
import { ToggleButton, Header, Item } from './subcomponents';

// Constants.
import {
  HIDDEN_PAGE_ALERT, MAKE_CURRENT_DATE_MESSAGE,
} from '../../modules/constants/application';
import { NO_NOTIFICATIONS_LABEL } from '../../modules/constants/date';
import { UNSEEN_STATUS } from '../../modules/constants/socketIO';
import {
  DIALOG_YES_TEXT, DIALOG_NO_TEXT,
} from '../../modules/constants/account';

// Actions.
import { getUser } from '../../actions/user';
import { getDates } from '../../actions/dates';
import { getSelectedPlaces } from '../../actions/places';
import { storePageAlert } from '../../actions/pageAlert';
import {
  updateNotification, updateNotifications,
} from '../../actions/notifications';

// Helpers.
import { NotificationHelper } from '../../modules/helpers';

// Services.
import {
  DateService, NotificationService, UserService,
} from '../../services';

// Styles.
import styles from './styles';
import { utilityStyles } from '../../styles';

// Destruct styles.
const { menu, itemList } = styles;
const { flex1, alignItemsCenter } = utilityStyles;

export default function NotificationsDropdown({ notifications }) {
  const dispatch = useDispatch();
  const user = useSelector(({ user }) => user);
  const [isOpen, setIsOpen] = useState(false);

  const unseenCount = notifications.filter(
    ({ status }) => status === UNSEEN_STATUS).length;

  const notificationsCount = notifications.length;

  const toggleIsOpen = () => setIsOpen(!isOpen);

  useEffect(() => {
    if (isOpen) {
      const unseenNotificationIds = NotificationHelper
        .getUnseenNotificationIds(notifications);

      NotificationService.updateMany(unseenNotificationIds)
        .then(() => (setTimeout(() => (
          dispatch(updateNotifications(unseenNotificationIds))
        ), 1000)))
        .catch(error => alert(error.message));
    }
  }, [isOpen]);

  const handleAcceptRequest = ({
    id, dateId, senderId, receiverId,
  }) => {
    DateService.updateDate(dateId, senderId, receiverId)
      .then(NotificationService.updateNotification(id))
      .then(() => {
        const pageAlert = {
          isShown: true,
          isConfirm: true,
          message: MAKE_CURRENT_DATE_MESSAGE,
          submitText: DIALOG_YES_TEXT,
          cancelText: DIALOG_NO_TEXT,
          handleSubmit: async () => {
            await UserService.updateUser({
              uid: receiverId,
              currentDateId: dateId
            });

            dispatch(getUser());

            dispatch(storePageAlert(HIDDEN_PAGE_ALERT));
          },
          handleCancel: () => dispatch(storePageAlert(HIDDEN_PAGE_ALERT))
        };

        return dispatch(storePageAlert(pageAlert));
      })
      .then(() => {
        const { uid } = user;
        dispatch(updateNotification(dateId));
        dispatch(getDates(user.uid));
        dispatch(getSelectedPlaces(uid, dateId));
      })
      .catch(error => alert(error.message));
  };

  const handleDeclineRequest = ({
    id, dateId, senderId, receiverId,
  }) => {
    DateService.deleteDate(dateId, senderId, receiverId)
      .then(NotificationService.updateNotification(id))
      .then(() => dispatch(updateNotification(dateId)))
      .catch(error => alert(error.message));
  };

  return (
    <View style={[flex1, alignItemsCenter]}>
      <Menu
        visible={isOpen}
        onDismiss={toggleIsOpen}
        anchor={(
          <ToggleButton
            isOpen={isOpen}
            unseenCount={unseenCount}
            handleOnPress={toggleIsOpen}
          />
        )}
        style={[menu]}
      >
        <Header />

        <Divider />

        <List.Section style={[itemList]}>
          {notificationsCount === 0 && (
            <List.Item title={NO_NOTIFICATIONS_LABEL} />
          )}

          {notifications.map((notification, index) => {
            const { type, status, message } = notification;
            return (
              <Fragment key={`${type}-${status}-${message}-${index}`}>
                <Item
                  notification={notification}
                  handleAccept={handleAcceptRequest}
                  handleDecline={handleDeclineRequest}
                />

                <Divider />
              </Fragment>
            );
          })}
        </List.Section>
      </Menu>
    </View>
  );
}
