import React, { Fragment, useState } from 'react';
import { useSelector } from 'react-redux';

// Components.
import { Platform } from 'react-native';
import { HelperText, TextInput } from 'react-native-paper';
import CommonDialog from '../CommonDialog';

// Constants.
import { INVALID_PASSWORD } from '../../modules/constants/authentication';

// Helpers.
import { ValidationHelper } from '../../modules/helpers';

// Styles.
import { utilityStyles } from '../../styles';

// Destruct helpers.
const { isPasswordInvalid } = ValidationHelper;

// Destruct styles.
const { textCenter, width100 } = utilityStyles;

export default function ConfirmPasswordDialog({
  isShown, handleOnDismiss, handleOnConfirm,
}) {
  const [password, setPassword] = useState('');
  const [isPasswordDirty, setIsDirty] = useState(false);
  const [isInvalid, setIsInvalid] = useState(false);

  const themeColor = useSelector(({ theme }) => theme.themeColor);

  const handlePasswordOnBlur = () => {
    setIsInvalid(isPasswordInvalid(password));
    setIsDirty(true);
  };

  const handlePasswordOnChange = password => {
    setPassword(password);
    if (isPasswordDirty) {
      setIsInvalid(isPasswordInvalid(password));
    }
  };

  const handleOnSubmit = () => {
    if (!isPasswordInvalid(password)) {
      handleOnConfirm(password.trim());
    }
  };

  return (
    <CommonDialog
      isShown={isShown}
      titleText={'Confirm password to continue'}
      isSubmitDisabled={!password || isInvalid}
      handleOnDismiss={handleOnDismiss}
      handleOnSubmit={handleOnSubmit}
      titleStyles={[textCenter]}
    >
      <Fragment>
        <TextInput
          value={password || ''}
          dense={true}
          autoFocus={true}
          mode="outlined"
          keyboardAppearance={themeColor}
          secureTextEntry={true}
          error={isInvalid}
          onBlur={handlePasswordOnBlur}
          onChangeText={handlePasswordOnChange}
          style={[width100]}
        />
        <HelperText
          type="error"
          visible={isInvalid}
          style={{ display: Platform.select({
            native: isPasswordInvalid ? 'flex' : 'none',
            default: 'flex'
          })}}
        >
          {INVALID_PASSWORD}
        </HelperText>
      </Fragment>
    </CommonDialog>
  );
}
