import {
  STORE_NOTIFICATIONS,
  REMOVE_NOTIFICATION,
} from '../modules/constants/actions';

export default function(state = [], action) {
  const { payload, type } = action;
  let updatedState = [].concat(state);

  switch (type) {
    case STORE_NOTIFICATIONS:
      updatedState = [].concat(payload);
      break;
    case REMOVE_NOTIFICATION:
      updatedState = [].concat(payload);
      break;
    default:
      updatedState = [].concat(state);
  }

  return updatedState;
}
