import React, { useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';

// Prop types.
import { accountScreenTypes } from '../../types';

// Components.
import { Button, Colors, List, Surface, withTheme } from 'react-native-paper';
import {
  AccountDialog, ConfirmPasswordDialog, ScreenHeader,
  ChangePasswordDialog,
} from '../../components';

// Styles.
import { utilityStyles } from '../../styles';

// Constants.
import {
  USER_DISPLAY_NAME_FIELD, USER_EMAIL_FIELD,
  SEARCH_RADIUS_FIELD,
} from '../../modules/constants/database';
import { HOME_ROUTE } from '../../modules/constants/authentication';
import {
  ACCOUNT_TITLE_TEXT, ACCOUNT_SUBTITLE_TEXT, DEFAULT_LOCATION_TEXT,
  DEFAULT_SEARCH_RADIUS,
} from '../../modules/constants/account';
import { TRASH_OUTLINE } from '../../modules/constants/icons';

// Actions.
import { getUser, logOut } from '../../actions/user';
import { setSearchRadius } from '../../actions/location';

// Services.
import { UserService } from '../../services';

// Helpers.
import { ValidationHelper } from '../../modules/helpers';

// Inputs data.
import getInputs from './inputs';

// Destruct styles.
const { container, innerContainer } = utilityStyles;

function Account({ navigation, theme: { colors } }) {
  const dispatch = useDispatch();
  const user = useSelector(state => state.user);
  const { searchRadius = DEFAULT_SEARCH_RADIUS } = useSelector(
    ({ location }) => location);

  const { displayName, email } = user;

  const [isConfirmDialogVisible, setIsConfirmDialogVisible] = useState(false);
  const [confirmCallback, setConfirmCallback] = useState(() => null);

  const [isAccountDialogVisible, setIsAccountDialogVisible] = useState(false);
  const [dialogContent, setDialogContent] = useState({});

  const [isPasswordDialogVisible, setIsPasswordDialogVisible] = useState(false);

  const handleShowDialog = content => {
    setDialogContent(content);
    setIsAccountDialogVisible(true);
  };

  const handleHideConfirmDialog = () => setIsConfirmDialogVisible(false);
  const handleHideAccountDialog = () => setIsAccountDialogVisible(false);

  const handleDialogInputOnBlur = event => {
    const { text } = event.nativeEvent;
    const { dbField } = dialogContent;

    const isInvalid = ValidationHelper.isInputInvalid(dbField, text);

    setDialogContent({
      ...dialogContent,
      isInputInvalid: isInvalid
    });

    return isInvalid;
  };

  const handleSubmitConfirmDialog = password => {
    UserService.authenticateUser(password)
      .then(confirmCallback)
      .catch(({ message }) => alert(message));
  };

  const handleSubmitAccountDialog = async value => {
    // Validate the input before submission.
    const trimmedValue = value.trim();
    const mockEvent = { nativeEvent: { text: trimmedValue } };
    const isInvalid = handleDialogInputOnBlur(mockEvent);
    if (isInvalid) return;

    const { dbField } = dialogContent;

    if (dbField === SEARCH_RADIUS_FIELD) {
      dispatch(setSearchRadius(trimmedValue));
      return navigation.navigate(HOME_ROUTE);
    }

    if (dbField === USER_DISPLAY_NAME_FIELD) {
      await UserService.updateProfile(trimmedValue, dbField);
    }

    if (dbField === USER_EMAIL_FIELD) {
      setConfirmCallback(() => async () => {
        await UserService.updateEmail(trimmedValue);
        dispatch(getUser());
        handleHideConfirmDialog();
      });
      setIsConfirmDialogVisible(true);
    }

    dispatch(getUser());
    handleHideAccountDialog();
  };

  const handleDeleteOnPress = () => {
    setIsConfirmDialogVisible(true);
    setConfirmCallback(() => async () => {
      await UserService.deleteAccount();
      dispatch(logOut());
    });
  };

  const inputs = getInputs(displayName, email, DEFAULT_LOCATION_TEXT, searchRadius);

  return (
    <Surface style={[container, { backgroundColor: colors.background }]}>
      {isAccountDialogVisible && (
        <AccountDialog
          isShown={isAccountDialogVisible}
          dialogContent={dialogContent}
          handleInputOnBlur={handleDialogInputOnBlur}
          handleOnDismiss={handleHideAccountDialog}
          handleOnSubmit={handleSubmitAccountDialog}
        />
      )}

      {isConfirmDialogVisible && (
        <ConfirmPasswordDialog
          isShown={isConfirmDialogVisible}
          handleOnDismiss={handleHideConfirmDialog}
          handleOnConfirm={handleSubmitConfirmDialog}
        />
      )}

      {isPasswordDialogVisible && (
        <ChangePasswordDialog
          isShown={isPasswordDialogVisible}
          handleOnDismiss={() => setIsPasswordDialogVisible(false)}
        />
      )}

      <ScreenHeader
        titleText={ACCOUNT_TITLE_TEXT}
        subheadingText={ACCOUNT_SUBTITLE_TEXT}
      />

      <List.Section style={[innerContainer, { backgroundColor: colors.surface }]}>
        {/* Username, Email, and Location */}
        {inputs.map(({
          title, description, icon, dbField, invalidMessage,
          keyboardType,
        }) => (
          <List.Item
            key={`${title}-settings`}
            title={title}
            // TODO: Fix this.
            description={(title === 'Location') ? DEFAULT_LOCATION_TEXT : description}
            left={() => <List.Icon icon={icon} />}
            onPress={() => handleShowDialog({
              title,
              description,
              dbField,
              invalidMessage,
              keyboardType,
              isInputInvalid: false
            })}
          />
        ))}

        <List.Item
          title={'Change password'}
          left={() => <List.Icon icon="lock-reset" />}
          onPress={() => setIsPasswordDialogVisible(true)}
        />
      </List.Section>

      <Button
        icon={TRASH_OUTLINE}
        mode="contained"
        color={Colors.red900}
        onPress={handleDeleteOnPress}
      >
        Delete Account
      </Button>
    </Surface>
  );
}

Account.propTypes = accountScreenTypes.propTypes;

export default withTheme(Account);
