// Constants.
import {
  USERS_API_ROUTE,
  NOTIFICATIONS_API_ROUTE,
  REQUEST_HEADERS,
} from '../modules/constants/api';

// Services.
import UserService from './UserService';

export default class NotificationService {
  static async getNotifications(userId) {
    const idToken = await UserService.getIdToken();

    return fetch(`${USERS_API_ROUTE}/${userId}/notifications`, {
      method: 'GET',
      headers: {
        ...REQUEST_HEADERS,
        Authorization: `Basic ${idToken}`
      },
    })
      .then(response => response.json())
      .catch(error => alert(error.message));
  }

  static async updateNotification(notificationId) {
    const idToken = await UserService.getIdToken();

    return fetch(`${NOTIFICATIONS_API_ROUTE}/${notificationId}`, {
      method: 'PUT',
      headers: {
        ...REQUEST_HEADERS,
        Authorization: `Basic ${idToken}`
      },
      body: JSON.stringify({ status: 'seen' })
    });
  }

  static async updateMany(notificationIds) {
    const idToken = await UserService.getIdToken();

    return notificationIds.map(id => (
      fetch(`${NOTIFICATIONS_API_ROUTE}/${id}`, {
        method: 'PUT',
        headers: {
          ...REQUEST_HEADERS,
          Authorization: `Basic ${idToken}`
        },
        body: JSON.stringify({ status: 'seen' })
      })
    ));
  }

  static async deleteNotification(notificationId) {
    const idToken = await UserService.getIdToken();

    return fetch(`${NOTIFICATIONS_API_ROUTE}/${notificationId}`, {
      method: 'DELETE',
      headers: {
        ...REQUEST_HEADERS,
        Authorization: `Basic ${idToken}`
      }
    });
  }
}
