import React from 'react';

// Prop types.
import { navigationSectionTypes } from '../../../types';

// Components.
import { MaterialCommunityIcons } from '@expo/vector-icons';
import { Drawer, withTheme } from 'react-native-paper';
import { DrawerItem } from '@react-navigation/drawer';

// Styles.
import styles from '../styles';

// Constants.
import {
  HOME_ROUTE, ACCOUNT_ROUTE
} from '../../../modules/constants/authentication';
import {
  HOME_OUTLINE_ICON, HOME_ICON,
  ACCOUNT_OUTLINE_ICON, ACCOUNT_ICON
} from '../../../modules/constants/icons';

// Destruct styles.
const { navigationSection } = styles;

function NavigationSection({
  currentRoute, theme, handleNavItemOnPress
}) {

  const homeIcon = currentRoute === HOME_ROUTE ? HOME_ICON : HOME_OUTLINE_ICON;

  const accountIcon = currentRoute ===
    ACCOUNT_ROUTE ? ACCOUNT_ICON : ACCOUNT_OUTLINE_ICON;

  const { colors: { background, surface }} = theme;
  const getBgColor = routeName => {
    return routeName === currentRoute ? background : surface;
  };

  return (
    <Drawer.Section style={[navigationSection]}>
      <DrawerItem
        icon={({ color, size }) => (
          <MaterialCommunityIcons
            name={homeIcon}
            color={color}
            size={size}
          />
        )}
        label={HOME_ROUTE}
        onPress={() => handleNavItemOnPress(HOME_ROUTE)}
        style={[{ backgroundColor: getBgColor(HOME_ROUTE) }]}
      />

      <DrawerItem
        icon={({ color, size }) => (
          <MaterialCommunityIcons
            name={accountIcon}
            color={color}
            size={size}
          />
        )}
        label={ACCOUNT_ROUTE}
        onPress={() => handleNavItemOnPress(ACCOUNT_ROUTE)}
        style={[{ backgroundColor: getBgColor(ACCOUNT_ROUTE) }]}
      />
    </Drawer.Section>
  );
}

NavigationSection.propTypes = navigationSectionTypes.propTypes;

export default withTheme(NavigationSection);
