import { bool, number, shape, string } from 'prop-types';
import themeFontTypes from './themeFont';

export default {
  propTypes: {
    mode: string,
    roundness: number,
    dark: bool,
    animation: shape({
      scale: number,
    }),
    fonts: shape({
      light: shape(themeFontTypes.propTypes),
      medium: shape(themeFontTypes.propTypes),
      regular: shape(themeFontTypes.propTypes),
      thin: shape(themeFontTypes.propTypes)
    }),
    colors: shape({
      accent: string,
      backdrop: string,
      background: string,
      border: string,
      card: string,
      disabled: string,
      error: string,
      notification: string,
      onBackground: string,
      onSurface: string,
      placeholder: string,
      primary: string,
      surface: string,
      text: string,
    }),
  }
};
