import React from 'react';

// Prop types.
import { authenticationScreenTypes } from '../types';

// Components.
import { Button, Surface, withTheme } from 'react-native-paper';
import {
  ScreenHeader, AuthenticationForm, EmailDialog,
} from '../components';

// Constants.'
import {
  ACCOUNT_CONSTANTS, APP_CONSTANTS, AUTHENTICATION_CONSTANTS,
} from '../modules/constants';

// Services.
import { UserService } from '../services';

// Styles.
import { utilityStyles } from '../styles';

// Destruct constants.
const { APP_NAME } = APP_CONSTANTS;
const {
  FORGOT_PASSWORD_TEXT,
  RESET_PASSWORD_TITLE_TEXT,
  RESET_PASSWORD_EMAIL_SENT,
  RESET_PASSWORD_PLACEHOLDER,
} = ACCOUNT_CONSTANTS;
const {
  LOGIN_SUBHEADING,
  SIGN_UP_SUBHEADING,
  LOGIN_ROUTE_TEXT,
  SIGN_UP_ROUTE_TEXT,
  LOGIN_ROUTE,
  SIGN_UP_ROUTE,
} = AUTHENTICATION_CONSTANTS;

// Destruct styles from stylesheet.
const { container } = utilityStyles;

function Authentication({ navigation, route, theme }) {
  const isSignUpPage = route.name === SIGN_UP_ROUTE;

  // Text based on the current page (login or sign up).
  const subheadingText = isSignUpPage ? SIGN_UP_SUBHEADING : LOGIN_SUBHEADING;
  const navigateText = isSignUpPage ? LOGIN_ROUTE_TEXT : SIGN_UP_ROUTE_TEXT;

  // When the link is pressed go to login or sign up page.
  const navigateAuthenticationOnPress = () => {
    const navigateToRoute = isSignUpPage ? LOGIN_ROUTE : SIGN_UP_ROUTE;
    navigation.navigate(navigateToRoute);
  };

  const handleEmailDialogOnSubmit = email => {
    UserService.sendPasswordResetEmail(email)
      .then(() => alert(`${RESET_PASSWORD_EMAIL_SENT} ${email}`))
      .catch(error => alert(error.message));
  };

  return (

    <Surface style={[container, { backgroundColor: theme.colors.background }]}>
      <ScreenHeader
        titleText={APP_NAME}
        subheadingText={subheadingText}
      />

      {/* Form for logging in and signing up. */}
      <AuthenticationForm isSignUpPage={isSignUpPage} />

      {!isSignUpPage && (
        <EmailDialog
          buttonMode="text"
          buttonText={FORGOT_PASSWORD_TEXT}
          titleText={RESET_PASSWORD_TITLE_TEXT}
          inputPlaceholder={RESET_PASSWORD_PLACEHOLDER}
          handleDialogOnSubmit={handleEmailDialogOnSubmit}
        />
      )}

      {/* Button to navigate to login and sign up pages. */}
      <Button onPress={navigateAuthenticationOnPress}>
        {navigateText}
      </Button>
    </Surface>
  );
}

Authentication.propTypes = authenticationScreenTypes.propTypes;

export default withTheme(Authentication);
