// Constants.
import { STORE_DATES } from '../modules/constants/actions';

// Services.
import DateService from '../services/DateService';

// Add Date.
export const addDate = date => {
  return (dispatch, getState) => {
    const { dates } = getState();

    const updatedDates = dates.map(date => ({ ...date }));
    updatedDates.push(date);

    dispatch({
      type: STORE_DATES,
      payload: updatedDates,
    });
  };
};

// Update Date.
export const updateDate = (dateId, updatedDate) => {
  return (dispatch, getState) => {
    const { dates } = getState();

    const updatedDates = dates.map(date => {
      let returnedDate = { ...date };

      if (date.id === dateId) {
        returnedDate = {
          ...date,
          ...updatedDate
        };
      }

      return returnedDate;
    });


    dispatch({
      type: STORE_DATES,
      payload: updatedDates,
    });
  };
};

// Remove Date.
export const removeDate = (dateId, requestorId, requestedId) => {
  return async (dispatch, getState) => {
    const { dates } = getState();

    await DateService.deleteDate(dateId, requestorId, requestedId);

    const updatedDates = dates
      .filter(({ id }) => id !== dateId)
      .map(date => ({ ...date }));

    dispatch({
      type: STORE_DATES,
      payload: updatedDates,
    });
  };
};

// Get Dates.
export const getDates = userId => {
  return async dispatch => {
    try {
      const dates = await DateService.getDates(userId);

      if (dates.error) {
        throw new Error(JSON.stringify(dates.error));
      }

      dispatch({
        type: STORE_DATES,
        payload: dates,
      });
    } catch (error) {
      alert(error);
    }
  };
};

// Get Date.
export const getDate = dateId => {
  return async dispatch => {
    try {
      const date = await DateService.getDate(dateId);

      dispatch(addDate(date));
    } catch (error) {
      alert(error);
    }
  };
};
