import AsyncStorage from '@react-native-community/async-storage';

// Action names.
import { SET_THEME_COLOR } from '../modules/constants/actions';

// Constants.
import {
  THEME_STORAGE_PROPERTY,
  LIGHT_MODE_NAME,
} from '../modules/constants/theme';

/**
 * Define Actions.
*/

// Set theme color.
export const setThemeColor = themeColor => {
  return async dispatch => {
    try {
      await AsyncStorage.setItem(THEME_STORAGE_PROPERTY, themeColor);

      dispatch({
        type: SET_THEME_COLOR,
        payload: themeColor,
      });
    } catch (error) {
      alert(error);
    }
  };
};

// Get theme color.
export const getThemeColor = () => {
  return async dispatch => {
    try {
      await AsyncStorage.getItem(THEME_STORAGE_PROPERTY)
        .then((themeColor = LIGHT_MODE_NAME) => {
          dispatch({
            type: SET_THEME_COLOR,
            payload: themeColor,
          });
        });
    } catch (error) {
      alert(error);
    }
  };
};

