import React, { Fragment } from 'react';

// Prop types.
import { themeTypes } from '../../../types';

// Components.
import { View } from 'react-native';
import { withTheme } from 'react-native-paper';

// Styles.
import styles from '../styles';
import { utilityStyles } from '../../../styles';

// Destruct styles.
const {
  loadingRating, loadingDataContainer, loadingData
} = styles;
const {
  flexRow, justifyContentAround, alignItemsCenter, width100
} = utilityStyles;

function LoadingContent({ theme }) {
  const { backdrop } = theme.colors;

  return (
    <Fragment>
      <View style={[loadingRating, { backgroundColor: backdrop }]} />

      <View style={[
        flexRow, justifyContentAround, alignItemsCenter,
        width100, loadingDataContainer
      ]}>
        <View style={[loadingData, { backgroundColor: backdrop }]} />
        <View style={[loadingData, { backgroundColor: backdrop }]} />
      </View>
    </Fragment>
  );
}

LoadingContent.propTypes = {
  ...themeTypes.propTypes,
};

export default withTheme(LoadingContent);
