// Constants.
import {
  USER_DISPLAY_NAME_FIELD, USER_EMAIL_FIELD,
  USER_LOCATION_FIELD, SEARCH_RADIUS_FIELD
} from '../constants/database';

export default class ValidationHelper {
  static isDisplayNameInvalid(displayName) {
    return !displayName ||
    displayName.trim().length < 3 ||
      !(/^(?=.*[^\W_])[\w ]*$/.test(displayName));
  }

  static isEmailInvalid(email) {
    return !(/^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(email));
  }

  static isPasswordInvalid(password) {
    return !password || password.trim().length < 6;
  }

  static isSearchRadiusInvalid(radius) {
    const radiusNumber = parseInt(radius, 10);
    return !(/^([1-9][0-9]*(\.[0-9]+)?|0+\.[0-9]*[1-9][0-9]*)$/g.test(radiusNumber));
  }

  static isInputInvalid(dbField, value) {
    let isInvalid = true;
    switch (dbField) {
      case USER_DISPLAY_NAME_FIELD:
        isInvalid = this.isDisplayNameInvalid(value);
        break;
      case USER_EMAIL_FIELD:
        isInvalid = this.isEmailInvalid(value);
        break;
      case USER_LOCATION_FIELD:
        isInvalid = false;
        break;
      case SEARCH_RADIUS_FIELD:
        isInvalid = this.isSearchRadiusInvalid(value);
        break;
      default:
        isInvalid = true;
        break;
    }

    return isInvalid;
  }
}
