import React from 'react';
import { useSelector } from 'react-redux';

// Components.
import { View } from 'react-native';
import { Caption, Title } from 'react-native-paper';

// Styles.
import styles from './styles';
import { utilityStyles } from '../../styles';

// Destruct styles.
const { infoContainer, title, caption } = styles;
const { marginVertical0, textLeft } = utilityStyles;

export default function InfoSection() {
  const { displayName, email } = useSelector(({ user }) => user);

  return (
    <View style={[infoContainer]}>
      <Title style={[textLeft, marginVertical0, title]}>
        {displayName}
      </Title>
      <Caption style={[textLeft, marginVertical0, caption]}>
        {email}
      </Caption>
    </View>
  );
}
