import React from 'react';

// Prop types.
import { pricingTypes } from '../../types';

// Components.
import { View } from 'react-native';
import { Text, withTheme } from 'react-native-paper';
import { MaterialCommunityIcons } from '@expo/vector-icons';

// Styles.
import styles from './styles';
import { utilityStyles } from '../../styles';

// Constants.
import { PRICING_TEXT_VALUES } from '../../modules/constants/google';
import { DOLLAR_SIGN_ICON } from '../../modules/constants/icons';

// Destruct styles.
const { outerView } = styles;
const { flexRow, justifyContentCenter, textCenter } = utilityStyles;

function Pricing({ pricingLevel, theme }) {
  const { colors } = theme;

  // Text used to display the pricing level.
  const pricingText = PRICING_TEXT_VALUES[pricingLevel];

  // Icons used to display the pricing level.
  const pricingIcons = [];
  for (let i = 0; i <= pricingLevel; i++) {
    pricingIcons.push(
      <MaterialCommunityIcons
        key={`pricing-icon-${i}`}
        name={DOLLAR_SIGN_ICON}
        color={colors.text}
        size={36}
      />
    );
  }

  return (
    <View style={[outerView]}>
      <View style={[flexRow, justifyContentCenter]}>
        {pricingIcons}
      </View>
      <Text style={[textCenter, { color: colors.text }]}>
        {pricingText}
      </Text>
    </View>
  );
}

Pricing.propTypes = pricingTypes.propTypes;

export default withTheme(Pricing);
