import React from 'react';
import socketIO from 'socket.io-client';

// Components.
import { MatchContent } from '../components';

// Actions.
import { addNotification } from '../actions/notifications';
import { updateDate, removeDate } from '../actions/dates';
import { storePageAlert } from '../actions/pageAlert';

// Constants.
import { SERVER_URL } from '../modules/constants/api';
import { DATE_ACCEPTED_STATUS } from '../modules/constants/date';
import {
  CONNECTED_EVENT,
  DISCONNECTED_EVENT,
  JOIN_ROOM_EVENT,
  DATE_REQUEST_EVENT,
  DATE_RESPONSE_EVENT,
  DATE_DECLINED_TYPE,
  DATE_MATCH_EVENT,
} from '../modules/constants/socketIO';

// Constants.
import {
  HIDDEN_PAGE_ALERT, DATE_MATCH_MESSAGE,
} from '../modules/constants/application';

// Store.
import store from '../reducers/store';

export default class SocketIO {
  constructor() {
    // Initialize Socket IO.
    this.socket = socketIO(SERVER_URL, {
      transports: ['websocket'],
      jsonp: false,
    });
  }

  start(userId) {
    this.socket.connect();

    this.socket.on(CONNECTED_EVENT, () => {
      console.log('connected to socket server');
      this.socket.emit('user', userId);
    });

    this.socket.on(DISCONNECTED_EVENT, () => {
      console.log('connection to server lost.');
    });

    this.socket.on(DATE_REQUEST_EVENT, notification => {
      store.dispatch(addNotification(notification));
    });

    this.socket.on(DATE_MATCH_EVENT, notification => {
      store.dispatch(addNotification(notification));

      const { place, senderName } = notification;

      const pageAlert = {
        isShown: true,
        isConfirm: false,
        message: DATE_MATCH_MESSAGE.replace('%name%', senderName),
        submitText: 'OK',
        handleSubmit: () => {
          store.dispatch(storePageAlert(HIDDEN_PAGE_ALERT));
        },
        handleCancel: () => {
          store.dispatch(storePageAlert(HIDDEN_PAGE_ALERT));
        },
        titleStyles: [{ textAlign: 'center' }],
        content: <MatchContent place={place} />,
      };

      store.dispatch(storePageAlert(pageAlert));
    });

    this.socket.on(DATE_RESPONSE_EVENT, notification => {
      store.dispatch(addNotification(notification));

      const { type, dateId } = notification;

      if (type === DATE_DECLINED_TYPE) {
        store.dispatch(removeDate(dateId));
      } else {
        store.dispatch(updateDate(
          dateId, { status: DATE_ACCEPTED_STATUS }));
      }
    });
  }

  joinRoom(roomName) {
    this.socket.emit(JOIN_ROOM_EVENT, roomName);
  }
}
