import {
  array, func, number, object, oneOfType, shape, string
} from 'prop-types';

// Other prop types.
import placeTypes from './place';

export default {
  propTypes: {
    cardIndex: number,
    placeData: oneOfType([
      object,
      shape({
        ...shape(placeTypes.propTypes),
        international_phone_number: string,
        photoUrl: string,
        photos: oneOfType([
          array,
          shape({
            html_attributions: string,
            photo_reference: string,
          })
        ]),
      })
    ]),
    handleChooseOnPress: func,
    handleRejectPlaceOnPress: func
  },
  defaultProps: {
    placeData: {}
  }
};
