import { bool, func, shape, string } from 'prop-types';

export default {
  propTypes: {
    isShown: bool,
    dialogContent: shape({
      dbField: string,
      description: string,
      invalidMessage: string,
      isInputInvalid: bool,
      title: string,
    }),
    handleInputOnBlur: func,
    handleOnDismiss: func,
    handleOnSubmit: func
  }
};
