import React from 'react';

// Components.
import { Platform, View } from 'react-native';
import { Button, Text } from 'react-native-paper';

import {
  FINISHED_TITLE,
  RESET_VIEWED_PLACES,
  INCREASE_SEARCH_RADIUS,
} from '../../modules/constants/place';

// Styles.
import { utilityStyles } from '../../styles';

// Destruct styles.
const { textCenter, width50, width100, marginHorizontalAuto } = utilityStyles;

export default function FinishedSwiping({
  handleResetViewedIds, handleIncreaseSearchRadius
}) {
  return (
    <View style={[{ width: '100%' }]}>
      <Text style={[textCenter, { fontSize: 18, marginVertical: 18 }]}>
        {FINISHED_TITLE}
      </Text>

      <View style={[marginHorizontalAuto, Platform.select({
        web: width50,
        native: width100
      })]}>
        <Button
          mode="outlined"
          onPress={handleResetViewedIds}
          style={[{ marginBottom: 25 }]}
        >{RESET_VIEWED_PLACES}</Button>

        <Button
          mode="outlined"
          onPress={handleIncreaseSearchRadius}
        >{INCREASE_SEARCH_RADIUS}</Button>
      </View>
    </View>
  );
}
