// Constants.
import { STORE_PAGE_ALERT } from '../modules/constants/actions';
import { HIDDEN_PAGE_ALERT } from '../modules/constants/application';

export default function(state = HIDDEN_PAGE_ALERT, action) {
  const { payload, type } = action;
  let updatedState = { ...state };

  switch (type) {
    case STORE_PAGE_ALERT:
      updatedState = { ...payload };
      break;
    default:
      updatedState = { ...state };
  }

  return updatedState;
}
