import React, { useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';

// Prop types.
import { authenticationFormTypes } from '../../types';

// Components.
import { Platform, View } from 'react-native';
import { Button, HelperText, Surface, TextInput, withTheme } from 'react-native-paper';

// Actions.
import {
  login, signUp, storeDisplayName, storeEmail, storePassword
} from '../../actions/user';

// Styles.
import { utilityStyles } from '../../styles';
import inputStyles from './styles';

// Helpers.
import { ValidationHelper } from '../../modules/helpers';

// Constants.
import {
  DISPLAY_NAME_PLACEHOLDER,
  EMAIL_PLACEHOLDER,
  PASSWORD_PLACEHOLDER,
  DISPLAY_NAME_INPUT_LABEL,
  EMAIL_INPUT_LABEL,
  PASSWORD_INPUT_LABEL,
  LOGIN_TEXT,
  SIGN_UP_TEXT,
  INVALID_DISPLAY_NAME,
  INVALID_EMAIL,
  INVALID_PASSWORD,
} from '../../modules/constants/authentication';

// Destruct styles.
const { innerContainer, width100 } = utilityStyles;
const { marginY15 } = inputStyles;

function AuthenticationForm ({ isSignUpPage, theme }) {
  const user = useSelector(state => state.user);
  const dispatch = useDispatch();
  const [isDisplayNameDirty, setIsDisplayNameDirty] = useState(false);
  const [isDisplayNameInvalid, setIsDisplayNameInvalid] = useState(false);
  const [isEmailDirty, setIsEmailDirty] = useState(false);
  const [isEmailInvalid, setIsEmailInvalid] = useState(false);
  const [isPasswordDirty, setIsPasswordDirty] = useState(false);
  const [isPasswordInvalid, setIsPasswordInvalid] = useState(false);

  const isInputsCompact = Platform.select({
    native: true,
    default: false,
  });

  // When submit is pressed login or sign up.
  const handleSubmitOnPress = () => {
    const dispatchMethod = isSignUpPage ? signUp : login;
    dispatch(dispatchMethod());
  };

  const handleDisplayNameOnBlur = () => {
    setIsDisplayNameInvalid(ValidationHelper.isDisplayNameInvalid(displayName));
    setIsDisplayNameDirty(true);
  };

  const handleDisplayNameOnChange = displayName => {
    dispatch(storeDisplayName(displayName));
    if (isDisplayNameDirty) {
      setIsDisplayNameInvalid(ValidationHelper.isDisplayNameInvalid(displayName));
    }
  };

  const handleEmailOnBlur = () => {
    setIsEmailInvalid(ValidationHelper.isEmailInvalid(email));
    setIsEmailDirty(true);
  };

  const handleEmailOnChange = email => {
    dispatch(storeEmail(email));
    if (isEmailDirty) {
      setIsEmailInvalid(ValidationHelper.isEmailInvalid(email));
    }
  };

  const handlePasswordOnBlur = () => {
    setIsPasswordInvalid(ValidationHelper.isPasswordInvalid(password));
    setIsPasswordDirty(true);
  };

  const handlePasswordOnChange = password => {
    dispatch(storePassword(password));
    if (isPasswordDirty) {
      setIsPasswordInvalid(ValidationHelper.isPasswordInvalid(password));
    }
  };

  const { displayName, email, password } = user;
  const submitText = isSignUpPage ? SIGN_UP_TEXT : LOGIN_TEXT;

  const isSubmitDisabled = (
    (isSignUpPage && !displayName) || !email || !password ||
    (isDisplayNameDirty && isDisplayNameInvalid) ||
    (isEmailDirty && isEmailInvalid) ||
    (isPasswordDirty && isPasswordInvalid)
  );

  return (
    <Surface style={[innerContainer, { backgroundColor: theme.colors.surface }]}>
      {/* Display Name Field */}
      {isSignUpPage && (
        <View style={[marginY15]}>
          <TextInput
            label={DISPLAY_NAME_INPUT_LABEL}
            value={displayName || ''}
            placeholder={DISPLAY_NAME_PLACEHOLDER}
            dense={isInputsCompact}
            mode="outlined"
            autoCapitalize="none"
            error={isDisplayNameInvalid}
            onBlur={handleDisplayNameOnBlur}
            onChangeText={handleDisplayNameOnChange}
            style={[width100]}
          />
          <HelperText
            type="error"
            visible={isDisplayNameInvalid}
            style={{ display: Platform.select({
              native: isDisplayNameInvalid ? 'flex' : 'none',
              default: 'flex'
            })}}
          >
            {INVALID_DISPLAY_NAME}
          </HelperText>
        </View>
      )}

      {/* Email Field */}
      <View style={[marginY15]}>
        <TextInput
          label={EMAIL_INPUT_LABEL}
          value={email || ''}
          placeholder={EMAIL_PLACEHOLDER}
          dense={isInputsCompact}
          mode="outlined"
          autoCapitalize="none"
          error={isEmailInvalid}
          onBlur={handleEmailOnBlur}
          onChangeText={handleEmailOnChange}
          style={[width100]}
        />
        <HelperText
          type="error"
          visible={isEmailInvalid}
          style={{ display: Platform.select({
            native: isEmailInvalid ? 'flex' : 'none',
            default: 'flex'
          })}}
        >
          {INVALID_EMAIL}
        </HelperText>
      </View>

      {/* Password Field */}
      <View style={[marginY15]}>
        <TextInput
          label={PASSWORD_INPUT_LABEL}
          value={password || ''}
          placeholder={PASSWORD_PLACEHOLDER}
          dense={isInputsCompact}
          mode="outlined"
          secureTextEntry={true}
          error={isPasswordInvalid}
          onBlur={handlePasswordOnBlur}
          onChangeText={handlePasswordOnChange}
          style={[width100]}
        />
        <HelperText
          type="error"
          visible={isPasswordInvalid}
          style={{ display: Platform.select({
            native: isPasswordInvalid ? 'flex' : 'none',
            default: 'flex'
          })}}
        >
          {INVALID_PASSWORD}
        </HelperText>
      </View>

      {/* Submit Button */}
      <Button
        disabled={isSubmitDisabled}
        mode="contained"
        onPress={handleSubmitOnPress}
        style={[width100, marginY15]}
      >
        {submitText}
      </Button>
    </Surface>
  );
}

AuthenticationForm.propTypes = authenticationFormTypes.propTypes;

export default withTheme(AuthenticationForm);
