import firebase from 'firebase';
import Firebase from '../config/Firebase';

// Constants.
import {
  USERS_API_ROUTE,
  REQUEST_HEADERS,
} from '../modules/constants/api';

export default class UserService {
  static getCurrentUser() {
    return Firebase.auth().currentUser;
  }

  static authenticateUser(password) {
    const currentUser = this.getCurrentUser();

    const credential = firebase.auth.EmailAuthProvider
      .credential(currentUser.email, password);

    return currentUser.reauthenticateWithCredential(credential);
  }

  static getIdToken() {
    const currentUser = this.getCurrentUser();
    return currentUser.getIdToken();
  }

  static authStateChanged() {
    return new Promise(resolve => {
      Firebase.auth().onAuthStateChanged(user => resolve(user));
    });
  }

  static signInEmail(email, password) {
    return Firebase.auth().signInWithEmailAndPassword(email, password);
  }

  static logOut() {
    return Firebase.auth().signOut();
  }

  static signUpEmail(email, password) {
    return Firebase.auth().createUserWithEmailAndPassword(email, password)
      .then(async ({ user: { uid }}) => {
        const idToken = await this.getIdToken();

        await fetch(`${USERS_API_ROUTE}/`, {
          method: 'POST',
          headers: {
            ...REQUEST_HEADERS,
            Authorization: `Basic ${idToken}`
          },
          body: JSON.stringify({ user: { uid } }),
        });

        return uid;
      });
  }

  static updateProfile(value, property) {
    const currentUser = this.getCurrentUser();
    return currentUser.updateProfile({ [property]: value });
  }

  static updateEmail(email) {
    const currentUser = this.getCurrentUser();
    return currentUser.updateEmail(email);
  }

  static updatePassword(password) {
    const currentUser = this.getCurrentUser();
    return currentUser.updatePassword(password);
  }

  static sendPasswordResetEmail(emailAddress) {
    return Firebase.auth().sendPasswordResetEmail(emailAddress);
  }

  static async deleteAccount() {
    const idToken = await this.getIdToken();
    const currentUser = this.getCurrentUser();

    return fetch(`${USERS_API_ROUTE}/${currentUser.uid}`, {
      method: 'DELETE',
      headers: {
        ...REQUEST_HEADERS,
        Authorization: `Basic ${idToken}`
      }
    })
      .then(() => currentUser.delete())
      .catch(error => alert(JSON.stringify(error)));
  }

  static async getUser() {
    const idToken = await this.getIdToken();
    const currentUser = this.getCurrentUser();

    return fetch(`${USERS_API_ROUTE}/${currentUser.uid}`, {
      method: 'GET',
      headers: {
        ...REQUEST_HEADERS,
        Authorization: `Basic ${idToken}`
      }
    })
      .then(response => response.json())
      .then(user => {
        const { uid, displayName, email } = currentUser;
        const { currentDateId } = user;
        return { uid, displayName, email, currentDateId };
      });
  }

  static async updateUser(user) {
    const idToken = await this.getIdToken();

    return fetch(`${USERS_API_ROUTE}/${user.uid}`, {
      method: 'PUT',
      headers: {
        ...REQUEST_HEADERS,
        Authorization: `Basic ${idToken}`
      },
      body: JSON.stringify({ user })
    });
  }
}
