import { SET_THEME_COLOR } from '../modules/constants/actions';

export default function(state = {}, action) {
  const { payload, type } = action;
  let updatedState = { ...state };

  switch (type) {
    case SET_THEME_COLOR:
      updatedState = {
        ...state,
        themeColor: payload,
      };
      break;
    default:
      updatedState = { ...state };
  }

  return updatedState;
}
