import React from 'react';

// Components.
import {
  Keyboard, Platform, TouchableWithoutFeedback, View,
  Dimensions,
} from 'react-native';
import { Button, Dialog, Portal } from 'react-native-paper';

// Constants.
import {
  DIALOG_CANCEL_TEXT, DIALOG_SUBMIT_TEXT
} from '../../modules/constants/account';

// Styles.
import { utilityStyles } from '../../styles';

// Destruct styles.
const {
  marginHorizontalAuto, marginBottom0, width50, width75
} = utilityStyles;

const { width } = Dimensions.get('window');

export default function CommonDialog({
  isShown, titleText, children, isSubmitDisabled, submitText,
  cancelText, isConfirmDialog, handleOnDismiss, handleOnSubmit,
  titleStyles = [],
}) {
  return (
    <Portal>
      <Dialog
        visible={isShown}
        onDismiss={handleOnDismiss}
        onPress={() => Keyboard.dismiss()}
        style={[Platform.select({
          web: [marginHorizontalAuto, width >= 768 ? width50 : width75]
        })]}
      >
        <TouchableWithoutFeedback onPress={() => Keyboard.dismiss()}>
          <View>
            <Dialog.Title style={[marginBottom0, ...titleStyles]}>
              {titleText}
            </Dialog.Title>

            {children && (
              <Dialog.Content>
                {children}
              </Dialog.Content>
            )}

            <Dialog.Actions>
              {isConfirmDialog && (
                <Button onPress={handleOnDismiss}>
                  {cancelText || DIALOG_CANCEL_TEXT}
                </Button>
              )}

              <Button
                disabled={isSubmitDisabled}
                onPress={handleOnSubmit}
              >
                {submitText || DIALOG_SUBMIT_TEXT}
              </Button>
            </Dialog.Actions>
          </View>
        </TouchableWithoutFeedback>
      </Dialog>
    </Portal>
  );
}
