// Constants.
import {
  NEARBY_SEARCH_URL,
  PLACE_DETAILS_URL,
  PHOTO_URL,
} from '../modules/constants/api';

// Constants.
import { REQUEST_HEADERS } from '../modules/constants/api';

// Helpers.
import { EquationHelper } from '../modules/helpers';

// Services.
import UserService from '../services/UserService';

// Destruct helper methods.
const { convertMilesToMeters } = EquationHelper;

export default class GoogleService {
  static async getNearbyPlaces(latitude, longitude, radius, pageToken) {
    const radiusMetersAmount = convertMilesToMeters(radius);

    const API_URL = `\
      ${NEARBY_SEARCH_URL}\
      ?latitude=${latitude}\
      &longitude=${longitude}\
      &radius=${radiusMetersAmount}\
      ${pageToken ? `&pagetoken=${pageToken}` : ''}\
    `;

    const idToken = await UserService.getIdToken();

    if (!idToken) {
      throw new Error('Invalid or missing ID Token');
    }

    return fetch(API_URL.replace(/ /g, ''), {
      headers: {
        ...REQUEST_HEADERS,
        Authorization: `Basic ${idToken}`
      }
    });
  }

  static async getPlaceDetailData(placeId) {
    const API_URL = `${PLACE_DETAILS_URL}?place_id=${placeId}`;

    const idToken = await UserService.getIdToken();

    if (!idToken) {
      throw new Error('Invalid or missing ID Token');
    }

    return fetch(API_URL, {
      headers: {
        ...REQUEST_HEADERS,
        Authorization: `Basic ${idToken}`
      }
    });
  }

  static async getPlaceImage(photoReference) {
    const API_URL = `${PHOTO_URL}?photo_reference=${photoReference}`;

    const idToken = await UserService.getIdToken();

    if (!idToken) {
      throw new Error('Invalid or missing ID Token');
    }

    return fetch(API_URL, {
      headers: {
        ...REQUEST_HEADERS,
        Authorization: `Basic ${idToken}`
      }
    });
  }
}
