Home.defaultProps = homeScreenTypes.defaultProps;
Home.propTypes = homeScreenTypes.propTypes;

const mapStateToProps = state => {
  return {
    user: state.user,
    places: state.places,
    location: state.location,
  };
};

const mapDispatchToProps = dispatch => {
  return bindActionCreators({
    updateViewedIds,
    updatePlaces,
    updateLocation,
    updateNextPageToken,
    storeSelectedPlace,
    removeSelectedPlace,
    addViewedPlaceId,
    getSearchRadius,
  }, dispatch);
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Home);
