import React, { useState } from 'react';
import { useSelector } from 'react-redux';

// Components.
import { Platform, View } from 'react-native';
import { Button, HelperText, TextInput } from 'react-native-paper';
import CommonDialog from '../CommonDialog';

// Constants.
import {
  DIALOG_SEND_TEXT,
} from '../../modules/constants/account';
import { INVALID_EMAIL } from '../../modules/constants/authentication';

// Helpers.
import { ValidationHelper } from '../../modules/helpers';

// Styles.
import { utilityStyles } from '../../styles';

// Destruct helpers.
const { isEmailInvalid } = ValidationHelper;

// Destruct styles.
const { textCenter, width100, marginBottom10 } = utilityStyles;

export default function ResetPasswordDialog({
  buttonMode, buttonText, buttonIcon, titleText,
  inputPlaceholder, children, handleDialogOnSubmit,
  buttonStyle = [], containerStyle = [],
}) {
  const [isShown, setIsShown] = useState(false);
  const [email, setEmail] = useState('');
  const [isDirty, setIsDirty] = useState(false);
  const [isInvalid, setIsInvalid] = useState(false);

  const themeColor = useSelector(({ theme }) => theme.themeColor);

  const handleShowDialog = () => setIsShown(true);
  const handleHideDialog = () => setIsShown(false);

  const handleOnBlur = () => {
    setIsInvalid(isEmailInvalid(email));
    setIsDirty(true);
  };

  const handleOnChange = email => {
    setEmail(email);
    if (isDirty) {
      setIsInvalid(isEmailInvalid(email));
    }
  };

  const handleOnSubmit = () => {
    const trimmedEmail = email.trim();
    handleDialogOnSubmit(trimmedEmail);
    handleHideDialog();
  };

  return (
    <View style={[...containerStyle]}>
      <Button
        mode={buttonMode}
        icon={buttonIcon}
        onPress={handleShowDialog}
        style={[...buttonStyle]}
      >
        {buttonText}
      </Button>
      <CommonDialog
        isShown={isShown}
        titleText={titleText}
        isSubmitDisabled={isInvalid || !email}
        submitText={DIALOG_SEND_TEXT}
        handleOnDismiss={handleHideDialog}
        handleOnSubmit={handleOnSubmit}
        titleStyles={[textCenter]}
      >
        <TextInput
          value={email || ''}
          placeholder={inputPlaceholder}
          dense={true}
          autoFocus={true}
          mode="outlined"
          autoCapitalize="none"
          keyboardType="email-address"
          keyboardAppearance={themeColor}
          error={isInvalid}
          onBlur={handleOnBlur}
          onChangeText={handleOnChange}
          style={[width100]}
        />
        <HelperText
          type="error"
          visible={isInvalid}
          style={[marginBottom10, { display: Platform.select({
            native: isInvalid ? 'flex' : 'none',
            default: 'flex'
          })}]}
        >
          {INVALID_EMAIL}
        </HelperText>

        {children && children}
      </CommonDialog>
    </View>
  );
}
