import React, { useState } from 'react';
import { useDispatch } from 'react-redux';

// Components.
import { View } from 'react-native';
import { IconButton, Menu } from 'react-native-paper';

// Actions.
import { removeNotification } from '../../../actions/notifications';

// Constants.
import { DOTS_VERTICAL, TRASH_OUTLINE } from '../../../modules/constants/icons';

// Service.
import { NotificationService } from '../../../services';

// Styles.
import styles from '../styles';
import { utilityStyles } from '../../../styles';

// Destruct styles.
const { submenu, submenuToggle } = styles;
const {
  cursorPointer, justifyContentCenter, margin0,
  paddingVertical0
} = utilityStyles;

export default function SubMenu({ notificationId }) {
  const dispatch = useDispatch();
  const [isOpen, setIsOpen] = useState(false);

  const toggleIsOpen = () => setIsOpen(!isOpen);

  const handleDeleteOnPress = () => {
    NotificationService.deleteNotification(notificationId)
      .then(() => dispatch(removeNotification(notificationId)))
      .catch(error => alert(error.message));
  };

  return (
    <View style={[justifyContentCenter]}>
      <Menu
        visible={isOpen}
        onDismiss={toggleIsOpen}
        anchor={(
          <IconButton
            icon={DOTS_VERTICAL}
            size={28}
            onPress={toggleIsOpen}
            style={[margin0, submenuToggle]}
          />
        )}
        contentStyle={[paddingVertical0]}
        style={[submenu]}
      >

        <Menu.Item
          title="Delete"
          icon={TRASH_OUTLINE}
          onPress={handleDeleteOnPress}
          style={[cursorPointer]}
        />
      </Menu>
    </View>
  );
}
