// Constants.
import { DATE_ACCEPTED_STATUS } from '../modules/constants/date';
import {
  USERS_API_ROUTE,
  DATES_API_ROUTE,
  REQUEST_HEADERS,
} from '../modules/constants/api';

// Services.
import UserService from './UserService';

export default class DateService {
  static async getDate(dateId) {
    const idToken = await UserService.getIdToken();

    return fetch(`${DATES_API_ROUTE}/${dateId}`, {
      method: 'GET',
      headers: {
        ...REQUEST_HEADERS,
        Authorization: `Basic ${idToken}`
      },
    }).then(response => response.json());
  }

  static async getDates(userId) {
    const idToken = await UserService.getIdToken();

    return fetch(`${USERS_API_ROUTE}/${userId}/dates`, {
      method: 'GET',
      headers: {
        ...REQUEST_HEADERS,
        Authorization: `Basic ${idToken}`
      },
    }).then(response => response.json());
  }

  static async createDate(date, requestedEmail, requestorName) {
    const idToken = await UserService.getIdToken();

    return fetch(DATES_API_ROUTE, {
      method: 'POST',
      headers: {
        ...REQUEST_HEADERS,
        Authorization: `Basic ${idToken}`
      },
      body: JSON.stringify({
        date,
        requestedEmail,
        requestorName,
      })
    });
  }

  static async updateDate(dateId, requestorId, requestedId) {
    const idToken = await UserService.getIdToken();
    const { displayName } = UserService.getCurrentUser();

    return fetch(`${DATES_API_ROUTE}/${dateId}`, {
      method: 'PUT',
      headers: {
        ...REQUEST_HEADERS,
        Authorization: `Basic ${idToken}`
      },
      body: JSON.stringify({
        requestorId,
        requestedId,
        requestedName: displayName,
        status: DATE_ACCEPTED_STATUS
      })
    });
  }

  static async deleteDate(dateId, requestorId, requestedId) {
    const idToken = await UserService.getIdToken();
    const { displayName } = UserService.getCurrentUser();

    return fetch(`${DATES_API_ROUTE}/${dateId}`, {
      method: 'DELETE',
      headers: {
        ...REQUEST_HEADERS,
        Authorization: `Basic ${idToken}`
      },
      body: JSON.stringify({
        requestorId,
        requestedId,
        requestedName: displayName,
      })
    });
  }
}
