import React from 'react';

// Components.
import { View } from 'react-native';
import { Button, Colors } from 'react-native-paper';

// Constants.
import {
  ACCEPT_LABEL,
  DECLINE_LABEL,
} from '../../../modules/constants/date';

// Styles.
import styles from '../styles';
import { utilityStyles } from '../../../styles';

// Destruct styles.
const { actionsContainer, actionButton } = styles;
const { flexRow, justifyContentAround } = utilityStyles;

export default function Actions({
  handleAcceptOnPress, handleDeclineOnPress,
}) {
  return (
    <View style={[flexRow, justifyContentAround, actionsContainer]}>
      <Button
        color={Colors.greenA700}
        onPress={handleAcceptOnPress}
        labelStyle={[actionButton]}
      >
        {ACCEPT_LABEL}
      </Button>

      <Button
        color={Colors.redA700}
        onPress={handleDeclineOnPress}
        labelStyle={[actionButton]}
      >
        {DECLINE_LABEL}
      </Button>
    </View>
  );
}
