import React, { Fragment } from 'react';

// Prop types.
import { themeTypes } from '../../../types';

// Components.
import { withTheme } from 'react-native-paper';
import { MaterialCommunityIcons } from '@expo/vector-icons';

// Constants.
import {
  CHECK_OUTLINE, CLOSE_OUTLINE
} from '../../../modules/constants/icons';

function LoadingActions({ theme }) {
  const { backdrop } = theme.colors;

  return (
    <Fragment>
      <MaterialCommunityIcons
        name={CHECK_OUTLINE}
        color={backdrop}
        size={48}
      />
      <MaterialCommunityIcons
        name={CLOSE_OUTLINE}
        color={backdrop}
        size={48}
      />
    </Fragment>
  );
}

LoadingActions.propTypes = {
  ...themeTypes.propTypes,
};

export default withTheme(LoadingActions);
