import React from 'react';

// Components.
import { View } from 'react-native';
import { Badge, IconButton, withTheme } from 'react-native-paper';

// Constants.
import { BELL_OUTLINE, BELL } from '../../../modules/constants/icons';

// Styles.
import styles from '../styles';
import { utilityStyles } from '../../../styles';

// Destruct styles.
const { badge } = styles;
const {
  flex1, flexRow, justifyContentCenter, alignItemsCenter,
  alignSelfCenter, margin0
} = utilityStyles;

function ToggleButton({
  theme: { colors }, isOpen, unseenCount, handleOnPress
}) {
  return (
    <View style={[flex1, flexRow, justifyContentCenter, alignItemsCenter]}>
      <IconButton
        icon={isOpen ? BELL : BELL_OUTLINE}
        color={colors.primary}
        size={24}
        animated={true}
        onPress={handleOnPress}
        style={[margin0]}
      />

      <View style={[{ width: 16 }]}>
        {unseenCount > 0 && (
          <Badge
            visible={true}
            size={16}
            style={[alignSelfCenter, badge]}
          >
            {unseenCount}
          </Badge>
        )}
      </View>
    </View>
  );
}

export default withTheme(ToggleButton);
