import React from 'react';

// Prop types.
import { logOutSectionTypes } from '../../../types';

// Components.
import { MaterialCommunityIcons } from '@expo/vector-icons';
import { Drawer } from 'react-native-paper';
import { DrawerItem } from '@react-navigation/drawer';

// Constants.
import { LOG_OUT_ROUTE } from '../../../modules/constants/authentication';
import { LOG_OUT_ICON } from '../../../modules/constants/icons';

function LogOutSection({ handleLogOutOnPress }) {
  return (
    <Drawer.Section>
      <DrawerItem
        icon={({ color, size }) => (
          <MaterialCommunityIcons
            name={LOG_OUT_ICON}
            color={color}
            size={size}
          />
        )}
        label={LOG_OUT_ROUTE}
        onPress={handleLogOutOnPress}
      />
    </Drawer.Section>
  );
}

LogOutSection.propTypes = logOutSectionTypes.propTypes;

export default LogOutSection;
