// Constants.
import {
  LOCATION_INPUT_LABEL,
  SEARCH_RADIUS_LABEL,
} from '../../modules/constants/account';

import {
  DISPLAY_NAME_ICON, EMAIL_ICON, LOCATION_ICON,
  SEARCH_RADIUS_ICON
} from '../../modules/constants/icons';

import {
  DISPLAY_NAME_INPUT_LABEL, EMAIL_INPUT_LABEL,
  INVALID_DISPLAY_NAME, INVALID_EMAIL, INVALID_LOCATION,
  INVALID_SEARCH_RADIUS,
} from '../../modules/constants/authentication';

import {
  USER_DISPLAY_NAME_FIELD,
  USER_EMAIL_FIELD,
  USER_LOCATION_FIELD,
  SEARCH_RADIUS_FIELD,
} from '../../modules/constants/database';

export default (displayName, email, location, searchRadius) => {
  return [{
    title: DISPLAY_NAME_INPUT_LABEL,
    description: displayName,
    icon: DISPLAY_NAME_ICON,
    dbField: USER_DISPLAY_NAME_FIELD,
    invalidMessage: INVALID_DISPLAY_NAME,
    keyboardType: 'default',
  },{
    title: EMAIL_INPUT_LABEL,
    description: email,
    icon: EMAIL_ICON,
    dbField: USER_EMAIL_FIELD,
    invalidMessage: INVALID_EMAIL,
    keyboardType: 'email-address',
  }, {
    title: LOCATION_INPUT_LABEL,
    description: location,
    icon: LOCATION_ICON,
    dbField: USER_LOCATION_FIELD,
    invalidMessage: INVALID_LOCATION,
    keyboardType: 'default',
  }, {
    title: SEARCH_RADIUS_LABEL,
    description: searchRadius.toString(),
    icon: SEARCH_RADIUS_ICON,
    dbField: SEARCH_RADIUS_FIELD,
    invalidMessage: INVALID_SEARCH_RADIUS,
    keyboardType: 'numeric',
  }];
};
