import React, { useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';

// Components.
import { View } from 'react-native';
import { Switch, Text } from 'react-native-paper';
import EmailDialog from '../EmailDialog';

// Actions.
import { addDate } from '../../actions/dates';
import { getSelectedPlaces } from '../../actions/places';
import { getUser } from '../../actions/user';

// Constants.
import { PLUS } from '../../modules/constants/icons';
import {
  ADD_DATE_LABEL,
  DATE_REQUEST_TITLE,
  DATE_REQUEST_INPUT_PLACEHOLDER,
} from '../../modules/constants/date';

// Services.
import { DateService, UserService } from '../../services';

// Styles.
import { utilityStyles } from '../../styles';

// Destruct styles.
const {
  flexRow, alignItemsCenter, marginHorizontal10
} = utilityStyles;

export default function DateRequestDialog() {
  const [isSwitchOn, setIsSwitchOn] = useState(true);
  const onToggleSwitch = () => setIsSwitchOn(!isSwitchOn);

  const dispatch = useDispatch();
  const { uid, displayName } = useSelector(({ user }) => user);
  const socketIO = useSelector(({ socketIO }) => socketIO);

  const handleEmailDialogOnSubmit = email => {
    const date = {
      userIdOne: uid,
      status: 'pending',
    };

    // Create the new date.
    DateService.createDate(date, email, displayName)
      .then(response => response.json())
      .then(async date => {
        if (date.error) {
          throw new Error(date.error.message);
        }

        const { id: dateId } = date;

        if (isSwitchOn) {
          await UserService.updateUser({
            uid,
            currentDateId: dateId,
          });

          dispatch(getUser());
        }

        dispatch(addDate(date));
        dispatch(getSelectedPlaces(uid, dateId));
        socketIO.joinRoom(dateId);
      })
      .catch(error => alert(error.message));
  };

  return (
    <EmailDialog
      buttonMode="outlined"
      buttonText={ADD_DATE_LABEL}
      buttonIcon={PLUS}
      titleText={DATE_REQUEST_TITLE}
      inputPlaceholder={DATE_REQUEST_INPUT_PLACEHOLDER}
      handleDialogOnSubmit={handleEmailDialogOnSubmit}
      buttonStyle={[{ borderWidth: 0 }]}
    >
      <View style={[flexRow, alignItemsCenter]}>
        <Switch
          value={isSwitchOn}
          onValueChange={onToggleSwitch}
        />
        <Text style={[marginHorizontal10]}>
          Make Current Date?
        </Text>
      </View>
    </EmailDialog>
  );
}
