import React, { Fragment, useState } from 'react';
import { useSelector } from 'react-redux';

// Components.
import { Platform } from 'react-native';
import { HelperText, TextInput } from 'react-native-paper';
import CommonDialog from '../CommonDialog';

export default function AccountDialog({
  isShown, dialogContent, handleInputOnBlur,
  handleOnDismiss, handleOnSubmit
}) {
  const themeColor = useSelector(({ theme }) => theme.themeColor);

  const {
    title, description, keyboardType,
    isInputInvalid, invalidMessage
  } = dialogContent;

  const [tempValue, setTempValue] = useState(description);
  const handleOnChangeText = value => setTempValue(value);

  return (
    <CommonDialog
      isShown={isShown}
      titleText={title}
      isSubmitDisabled={isInputInvalid || tempValue === description}
      handleOnDismiss={handleOnDismiss}
      handleOnSubmit={() => handleOnSubmit(tempValue)}
    >
      <Fragment>
        <TextInput
          key={title}
          autoFocus={true}
          value={tempValue}
          dense={true}
          keyboardType={keyboardType}
          keyboardAppearance={themeColor}
          mode="outlined"
          autoCapitalize="none"
          textAlign="left"
          error={isInputInvalid}
          onBlur={handleInputOnBlur}
          onChangeText={handleOnChangeText}
        />

        <HelperText
          type="error"
          visible={isInputInvalid}
          style={{ display: Platform.select({
            native: isInputInvalid ? 'flex' : 'none',
            default: 'flex'
          })}}
        >
          {invalidMessage}
        </HelperText>
      </Fragment>
    </CommonDialog>
  );
}
