import React, { Component } from 'react';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';

// Prop types.
import { placeSwiperTypes } from '../../types';

// Components.
import { Dimensions, Platform, StyleSheet, View } from 'react-native';
import Swiper from 'react-native-deck-swiper';
import PlaceCard from '../PlaceCard';
import FinishedSwiping from '../FinishedSwiping';

// Actions.
import { updateViewedIds } from '../../actions/places';

// Styles.
import styles from './styles';
import { utilityStyles } from '../../styles';

// Destruct styles.
const { swiperContainer, cardStyle } = styles;
const { flex1, width100 } = utilityStyles;

// The width of the screen.
const { width } = Dimensions.get('window');

class PlaceSwiper extends Component {
  constructor(props) {
    super(props);

    this.swiper = React.createRef();

    this.state = {
      isAllSwiped: false,
    };

    // Bind class methods.
    this.handleBackOnPress = this.handleBackOnPress.bind(this);
    this.handleRejectPlaceOnPress = this.handleRejectPlaceOnPress.bind(this);
    this.handleResetViewedIds = this.handleResetViewedIds.bind(this);
    this.handleIncreaseSearchRadius = this.handleIncreaseSearchRadius.bind(this);
  }

  handleRejectPlaceOnPress(cardIndex) {
    const {
      cards,
      handleRemoveAcceptedPlace,
    } = this.props;

    const { place_id } = cards[cardIndex];

    handleRemoveAcceptedPlace(place_id);

    this.swiper.swipeLeft();
  }

  handleBackOnPress() {
    const { currentCardIndex, updateCardIndex } = this.props;

    this.swiper.swipeBottom(true);
    updateCardIndex(currentCardIndex - 1);
  }

  handleResetViewedIds() {
    this.setState(() => ({ isAllSwiped: false }),
      this.props.handleResetViewedPlaces);
  }

  handleIncreaseSearchRadius() {
    const { navigation } = this.props;

    this.setState(() => ({ isAllSwiped: false }),
      () => navigation.navigate('Account'));
  }

  render() {
    const { isAllSwiped } = this.state;

    const {
      cards, currentCardIndex, updateNextPlace,
      handleChoosePlace,
    } = this.props;

    const isSecondCardShown = Platform.OS !== 'web';
    const quarterWidth = width / 4;

    const RenderedCard = (card, cardIndex) => (
      <PlaceCard
        placeData={card}
        cardIndex={cardIndex}
        handleChooseOnPress={() => this.swiper.swipeRight()}
        handleRejectPlaceOnPress={this.handleRejectPlaceOnPress}
        handleBackOnPress={this.handleBackOnPress}
      />
    );

    const handleSwipedRight = cardIndex => {
      handleChoosePlace(cardIndex);
      updateNextPlace(cardIndex);
    };

    return (
      <View style={[flex1, width100]}>
        {cards && cards.length ? (
          <View style={[flex1, width100]}>
            {isAllSwiped ? (
              <FinishedSwiping
                handleResetViewedIds={this.handleResetViewedIds}
                handleIncreaseSearchRadius={this.handleIncreaseSearchRadius}
              />
            ) : (
              <Swiper
                key={`${cards.length}-${isAllSwiped}`}
                ref={swiper => (this.swiper = swiper)}
                cards={cards}
                stackSize= {3}
                cardIndex={currentCardIndex}
                showSecondCard={isSecondCardShown}
                verticalSwipe={false}
                infinite={false}
                stackSeparation={40}
                stackScale={10}
                stackAnimationFriction={5}
                stackAnimationTension={80}
                inputRotationRange={[-quarterWidth, 0, quarterWidth]}
                outputRotationRange={['-15deg', '0deg', '15deg']}
                animateCardOpacity={true}
                renderCard={RenderedCard}
                onSwipedRight={handleSwipedRight}
                onSwipedLeft={updateNextPlace}
                onSwipedAll={() => this.setState(() => ({ isAllSwiped: true }))}
                backgroundColor="transparent"
                containerStyle={StyleSheet.flatten(swiperContainer)}
                cardStyle={cardStyle}
              />
            )}
          </View>
        ) : (null)}
      </View>
    );
  }
}

PlaceSwiper.propTypes = placeSwiperTypes.propTypes;

const mapStateToProps = state => {
  return {
    user: state.user,
  };
};

const mapDispatchToProps = dispatch => {
  return bindActionCreators({
    updateViewedIds,
  }, dispatch);
};

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(PlaceSwiper);
