import AsyncStorage from '@react-native-community/async-storage';

// Constants.
import {
  DEFAULT_SEARCH_RADIUS,
  SEARCH_RADIUS_STORAGE_PROPERTY,
} from '../modules/constants/account';
import {
  UPDATE_LOCATION,
  UPDATE_SEARCH_RADIUS,
  UPDATE_NEXT_PAGE_TOKEN
} from '../modules/constants/actions';

/**
 * Define Actions.
*/

// Update Location.
export const updateLocation = coords => {
  return {
    type: UPDATE_LOCATION,
    payload: coords,
  };
};

// Update Search Radius.
export const updateSearchRadius = searchRadius => {
  return {
    type: UPDATE_SEARCH_RADIUS,
    payload: searchRadius,
  };
};

// Update Next Page Token.
export const updateNextPageToken = pageToken => {
  return {
    type: UPDATE_NEXT_PAGE_TOKEN,
    payload: pageToken,
  };
};

// Set Search Radius.
export const setSearchRadius = searchRadius => {
  return async dispatch => {
    try {
      await AsyncStorage.setItem(SEARCH_RADIUS_STORAGE_PROPERTY, searchRadius);

      dispatch({
        type: UPDATE_SEARCH_RADIUS,
        payload: searchRadius,
      });
    } catch (error) {
      alert(error);
    }
  };
};

// Get Search Radius.
export const getSearchRadius = () => {
  return async dispatch => {
    try {
      await AsyncStorage.getItem(SEARCH_RADIUS_STORAGE_PROPERTY)
        .then((searchRadius = DEFAULT_SEARCH_RADIUS) => {
          dispatch({
            type: UPDATE_SEARCH_RADIUS,
            payload: searchRadius || DEFAULT_SEARCH_RADIUS,
          });
        });
    } catch (error) {
      alert(error);
    }
  };
};
