import {
  UPDATE_PLACES,
  ADD_SEEN_PLACE_ID,
  UPDATE_VIEWED_IDS,
  STORE_SELECTED_PLACE,
} from '../modules/constants/actions';

export default function(state = {}, action) {
  const { payload, type } = action;
  let updatedState = { ...state };

  switch (type) {
    case UPDATE_PLACES:
      updatedState = {
        ...state,
        all: payload,
      };
      break;
    case ADD_SEEN_PLACE_ID:
      updatedState = {
        ...state,
        viewedIds: payload,
      };
      break;
    case UPDATE_VIEWED_IDS:
      updatedState = {
        ...state,
        viewedIds: payload,
      };
      break;
    case STORE_SELECTED_PLACE:
      updatedState = {
        ...state,
        selected: payload,
      };
      break;
    default:
      updatedState = { ...state };
  }

  return updatedState;
}
