import React from 'react';

// Components.
import { Dimensions, ScrollView } from 'react-native';
import { Colors, List } from 'react-native-paper';
import { MaterialCommunityIcons } from '@expo/vector-icons';

// Constants.
import { NO_SELECTED_PLACES_MESSAGE } from '../modules/constants/place';
import { TRASH_OUTLINE } from '../modules/constants/icons';

// Styles.
import { utilityStyles } from '../styles';

// Destruct styles.
const { marginVerticalAuto, textCenter } = utilityStyles;

function AcceptedPlaces() {
  // TODO: get from and store in redux.
  const places = [];

  const height = Dimensions.get('window').height - 300;

  return (
    <ScrollView
      style={{ flex: 1 }}
      contentContainerStyle={[{ height }]}
      scrollEnabled={true}
    >
      <List.Section>
        {!places.length && (
          <List.Item
            title={NO_SELECTED_PLACES_MESSAGE}
            titleStyle={[textCenter]}
          />
        )}

        {places.map(({
          name, vicinity
        }, index) => (
          <List.Item
            key={`${name}-${index}`}
            title={name}
            description={vicinity}
            right={() => (
              <MaterialCommunityIcons
                name={TRASH_OUTLINE}
                color={Colors.red900}
                size={32}
                onPress={() => alert('remove selected place')}
                style={[marginVerticalAuto]}
              />
            )}
          />
        ))}
      </List.Section>
    </ScrollView>
  );
}

export default AcceptedPlaces;
