// Constants.
import {
  STORE_NOTIFICATIONS,
  REMOVE_NOTIFICATION,
} from '../modules/constants/actions';

// Services.
import NotificationService from '../services/NotificationService';

// Add Notification.
export const addNotification = notification => {
  return (dispatch, getState) => {
    const { notifications } = getState();

    const updatedNotifications = notifications.map(
      notification => ({ ...notification }));

    updatedNotifications.push(notification);

    dispatch({
      type: STORE_NOTIFICATIONS,
      payload: updatedNotifications,
    });
  };
};

// Update Notification.
export const updateNotification = dateId => {
  return (dispatch, getState) => {
    const { notifications } = getState();

    const updatedNotifications = notifications
      .map(notification => {
        const { dateId: id } = notification;

        let updatedNotification = { ...notification };

        if (id === dateId) {
          updatedNotification = {
            ...notification,
            status: 'seen',
          };
        }

        return updatedNotification;
      });

    dispatch({
      type: STORE_NOTIFICATIONS,
      payload: updatedNotifications,
    });
  };
};

// Update Many Notifications.
export const updateNotifications = notificationIds => {
  return (dispatch, getState) => {
    const { notifications } = getState();

    const updatedNotifications = notifications
      .map(notification => {
        const { id } = notification;

        let updatedNotification = { ...notification };

        if (notificationIds.includes(id)) {
          updatedNotification = {
            ...notification,
            status: 'seen',
          };
        }

        return updatedNotification;
      });

    dispatch({
      type: STORE_NOTIFICATIONS,
      payload: updatedNotifications,
    });
  };
};

// Remove Notification.
export const removeNotification = notificationId => {
  return (dispatch, getState) => {
    const { notifications } = getState();

    const updatedNotifications = notifications
      .filter(({ id }) => id !== notificationId)
      .map(notification => ({ ...notification }));

    dispatch({
      type: REMOVE_NOTIFICATION,
      payload: updatedNotifications,
    });
  };
};

// Get Notifications.
export const getNotifications = userId => {
  return async dispatch => {
    try {
      const notifications = await NotificationService.getNotifications(userId);

      if (notifications.error) {
        throw new Error(JSON.stringify(notifications.error));
      }

      dispatch({
        type: STORE_NOTIFICATIONS,
        payload: notifications,
      });
    } catch (error) {
      alert(error);
    }
  };
};
