import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { createDrawerNavigator } from '@react-navigation/drawer';

// React Navigation.
import {
  NavigationContainer,
  DarkTheme as NavigationDarkTheme,
  DefaultTheme as NavigationDefaultTheme,
} from '@react-navigation/native';

// React Native Paper.
import {
  DarkTheme as PaperDarkTheme,
  DefaultTheme as PaperDefaultTheme,
  Provider as PaperProvider,
} from 'react-native-paper';
import merge from 'deepmerge';

// Navigation.
import { RootNavigator, DrawerContent } from './navigation';

// Components.
import { CommonDialog } from './components';

// Actions.
import { getThemeColor } from './actions/theme';

// Constants.
import { APP_NAME } from './modules/constants/application';
import { DARK_MODE_NAME } from './modules/constants/theme';

const CombinedDefaultTheme = merge(PaperDefaultTheme, NavigationDefaultTheme);
const CombinedDarkTheme = merge(PaperDarkTheme, NavigationDarkTheme);

const Drawer = createDrawerNavigator();

const formatter = (options, route) => (
  `${options?.title ?? route?.name} - ${APP_NAME}`
);

export default function Main() {
  const dispatch = useDispatch();
  const userId = useSelector(({ user }) => user.uid);
  const themeColor = useSelector(({ theme }) => theme.themeColor);
  const pageAlert = useSelector(({ pageAlert }) => pageAlert);

  const [currentRoute, setCurrentRoute] = useState(null);

  useEffect(() => {
    dispatch(getThemeColor());
  }, [themeColor]);

  const theme = themeColor === DARK_MODE_NAME ? CombinedDarkTheme : CombinedDefaultTheme;

  // Gets the current screen from navigation state.
  // Thanks to: https://stackoverflow.com/a/61211256/11878783.
  const getActiveRouteName = state => {
    if (!state) return;
    const route = state.routes[state?.index || 0];

    if (route.state) {
      // Dive into nested navigators
      return getActiveRouteName(route.state);
    }

    setCurrentRoute(route.name);
  };

  return (
    <PaperProvider theme={theme}>
      <NavigationContainer
        theme={theme}
        documentTitle={{ formatter }}
        onStateChange={getActiveRouteName}
      >
        {pageAlert.isShown && (
          <CommonDialog
            isShown={pageAlert.isShown}
            titleText={pageAlert.message}
            isConfirmDialog={pageAlert.isConfirm}
            submitText={pageAlert.submitText}
            cancelText={pageAlert.cancelText}
            handleOnDismiss={pageAlert.handleCancel}
            handleOnSubmit={pageAlert.handleSubmit}
            titleStyles={pageAlert.titleStyles || []}
          >
            {pageAlert.content || null}
          </CommonDialog>
        )}

        {userId ? (
          <Drawer.Navigator
            drawerType="slide"
            drawerContent={props => (
              <DrawerContent currentRoute={currentRoute} {...props} />
            )}
          >
            <Drawer.Screen
              name="Root"
              component={RootNavigator}
            />
          </Drawer.Navigator>
        ) : (
          <RootNavigator />
        )}
      </NavigationContainer>
    </PaperProvider>
  );
}
