import React from 'react';
import { useSelector } from 'react-redux';

// Prop types.
import { navigationTypes, routeTypes } from '../types';

// Components.
import { View, Platform } from 'react-native';
import { Appbar, IconButton, withTheme } from 'react-native-paper';
import { Link } from '@react-navigation/native';
import { NotificationsDropdown } from '../components';

// Styles.
import { utilityStyles } from '../styles';

// Constants.
import { APP_NAME } from '../modules/constants/application';
import { HOME_ROUTE, LOGIN_ROUTE } from '../modules/constants/authentication';
import { MENU_ICON } from '../modules/constants/icons';

// Destruct styles.
const {
  width100, textCenter, margin0, marginHorizontal10, flexRow,
  justifyContentBetween, alignItemsCenter, flex1, flex4,
} = utilityStyles;

function NavHeader({ scene, navigation, theme }) {
  const userId = useSelector(({ user }) => user.uid);
  const notifications = useSelector(({ notifications }) => (
    notifications.sort((x, y) => y.timestamp - x.timestamp)
  ));

  const { colors } = theme;

  return (
    <Appbar.Header theme={{ colors: { primary: colors.surface } }}>
      <View style={[width100, flexRow, justifyContentBetween, alignItemsCenter]}>
        <View style={[flex1]}>
          {userId && (
            <IconButton
              icon={MENU_ICON}
              color={colors.primary}
              size={40}
              onPress={navigation.openDrawer}
              style={[margin0]}
            />
          )}
        </View>

        <Appbar.Content
          title={(
            <Link to={`/${userId ? HOME_ROUTE : LOGIN_ROUTE}`}>
              {APP_NAME}
            </Link>
          )}
          subtitle={Platform.OS !== 'web' ? scene.route.name : ''}
          color={colors.text}
          titleStyle={[textCenter]}
          subtitleStyle={[textCenter]}
          style={[marginHorizontal10, flex4]}
        />

        <View style={[flex1]}>
          {userId && (
            <NotificationsDropdown notifications={notifications} />
          )}
        </View>
      </View>
    </Appbar.Header>
  );
}

NavHeader.propTypes = {
  ...navigationTypes.propTypes,
  ...routeTypes.propTypes,
};

export default withTheme(NavHeader);
