import { array, func, number, oneOfType, shape } from 'prop-types';

// Import other prop types.
import placeTypes from './place';

export default {
  propTypes: {
    currentCardIndex: number,
    cards: oneOfType([
      array,
      shape(placeTypes.propTypes)
    ]),
    updateNextPlace: func,
    handleChoosePlace: func,
    handleRemoveAcceptedPlace: func,
  }
};
