import React from 'react';
import moment from 'moment/src/moment';

// Components.
import { View } from 'react-native';
import { Text, withTheme } from 'react-native-paper';

// Styles.
import styles from '../styles';
import { utilityStyles } from '../../../styles';

// Destruct styles.
const { time } = styles;
const {
  flexRow, flex1, flex2, justifyContentBetween,
  alignItemsCenter, width100, textRight
} = utilityStyles;

function Title({
  senderName, timestamp, theme,
}) {
  const { colors: { text: color } } = theme;

  const timeAgo = moment(timestamp).fromNow();

  return (
    <View style={[flexRow, justifyContentBetween, alignItemsCenter, width100]}>
      <Text style={[flex2]}>
        {senderName}
      </Text>

      <Text style={[flex1, textRight, time, { color }]}>
        {timeAgo}
      </Text>
    </View>
  );
}

export default withTheme(Title);
