import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';

// Components.
import { MaterialCommunityIcons } from '@expo/vector-icons';
import { DrawerItem } from '@react-navigation/drawer';
import { Drawer, Switch } from 'react-native-paper';

// Styles.
import { utilityStyles } from '../../styles/';

// Actions.
import { setThemeColor } from '../../actions/theme';

// Constants.
import { DARK_MODE_ICON } from '../../modules/constants/icons';
import {
  DARK_MODE_NAME,
  DARK_MODE_LABEL,
  LIGHT_MODE_NAME,
} from '../../modules/constants/theme';

// Destruct styles.
const { flexRow, alignItemsCenter } = utilityStyles;

export default function ThemeSwitch() {
  const dispatch = useDispatch();
  const themeColor = useSelector(state => state.theme.themeColor);

  const [isDarkMode, setIsDarkMode] = useState(themeColor === DARK_MODE_NAME);
  const handleSwitchOnPress = () => setIsDarkMode(!isDarkMode);

  useEffect(() => {
    const newThemeColor = isDarkMode ? DARK_MODE_NAME : LIGHT_MODE_NAME;
    if (newThemeColor !== themeColor) {
      dispatch(setThemeColor(newThemeColor));
    }
  }, [isDarkMode]);

  return (
    <Drawer.Section style={[flexRow, alignItemsCenter]}>
      <DrawerItem
        icon={({ color, size }) => (
          <MaterialCommunityIcons
            name={DARK_MODE_ICON}
            color={color}
            size={size}
          />
        )}
        label={DARK_MODE_LABEL}
        onPress={handleSwitchOnPress}
        style={[{ flex: 1 }]}
      />

      <Switch
        value={isDarkMode}
        onValueChange={handleSwitchOnPress}
        style={[{ marginHorizontal: 8 }]}
      />
    </Drawer.Section>
  );
}
