import { Colors } from 'react-native-paper';

// Constants.
import {
  STAR_ICON,
  EMPTY_STAR_ICON,
  HALF_STAR_ICON,
} from '../constants/icons';

export default class RatingHelper {
  static getStarIcon(ratingValue, starNumber) {
    const ratingRemainder = ratingValue % 1;
    const isRatingDecimal = ratingRemainder !== 0;
    const ratingWholeNumber = Math.floor(ratingValue);

    let starIcon = STAR_ICON;

    if (starNumber > ratingWholeNumber) {
      starIcon = EMPTY_STAR_ICON;

      if (isRatingDecimal && starNumber - 1 === ratingWholeNumber) {
        starIcon = HALF_STAR_ICON;

        if (ratingRemainder <= 0.29) {
          starIcon = EMPTY_STAR_ICON;
        } else if (ratingRemainder >= 0.79) {
          starIcon = STAR_ICON;
        }
      }
    }

    return starIcon;
  }

  static getRatingColor(ratingValue) {
    let ratingColor = Colors.green500;

    if (ratingValue <= 2.29) {
      ratingColor = Colors.red500;
    } else if (ratingValue <= 3.29) {
      ratingColor = Colors.yellow500;
    }

    return ratingColor;
  }
}
