import React from 'react';
import { useDispatch, useSelector } from 'react-redux';

// Components.
import { Dimensions, ScrollView } from 'react-native';
import { Colors, IconButton, List, Surface, withTheme } from 'react-native-paper';
import { CommonDialog, DateRequestDialog } from '../components';

// Actions.
import { storePageAlert } from '../actions/pageAlert';
import { getUser } from '../actions/user';

// Constants.
import { NO_DATES_MESSAGE } from '../modules/constants/date';
import {
  DIALOG_YES_TEXT, DIALOG_NO_TEXT,
} from '../modules/constants/account';
import { HIDDEN_PAGE_ALERT } from '../modules/constants/application';

// Services.
import { UserService } from '../services';

// Styles.
import { utilityStyles } from '../styles';

// Destruct styles.
const { container, innerContainer, textCenter } = utilityStyles;

function Dates({ theme: { colors } }) {
  const dispatch = useDispatch();
  const user = useSelector(({ user }) => user);
  const dates = useSelector(({ dates }) => dates);
  const pageAlert = useSelector(({ pageAlert }) => pageAlert);

  const showConfirmAction = (message, handleSubmit) => {
    const pageAlert = {
      isShown: true,
      isConfirm: true,
      message,
      submitText: DIALOG_YES_TEXT,
      cancelText: DIALOG_NO_TEXT,
      titleStyles: [textCenter],
      handleSubmit: handleSubmit,
      handleCancel: () => dispatch(storePageAlert(HIDDEN_PAGE_ALERT))
    };

    return dispatch(storePageAlert(pageAlert));
  };

  const handleMakeCurrent = dateId => {
    showConfirmAction('Do you want to make this your current date', async () => {
      await UserService.updateUser({
        ...user,
        currentDateId: dateId,
      });

      dispatch(getUser());
      dispatch(storePageAlert(HIDDEN_PAGE_ALERT));
    });
  };

  const height = Dimensions.get('window').height - 300;

  return (
    <ScrollView
      style={{ flex: 1 }}
      contentContainerStyle={[{ height }]}
      scrollEnabled={true}
    >
      <Surface style={[container, { justifyContent: 'flex-start', backgroundColor: colors.background }]}>
        <CommonDialog
          isShown={pageAlert.isShown}
          titleText={pageAlert.message}
          isConfirmDialog={pageAlert.isConfirm}
          submitText={pageAlert.submitText}
          cancelText={pageAlert.cancelText}
          handleOnDismiss={pageAlert.handleCancel}
          handleOnSubmit={pageAlert.handleSubmit}
          titleStyles={pageAlert.titleStyles || []}
        />

        <List.Section style={[innerContainer, { backgroundColor: colors.surface }]}>
          <List.Subheader style={[textCenter, { padding: 0 }]}>
            <DateRequestDialog />
          </List.Subheader>

          {dates.length === 0 && (
            <List.Item
              title={NO_DATES_MESSAGE}
              titleStyle={[textCenter]}
            />
          )}

          {dates.map(({ id, userIdOne, otherUserName, status }, index) => (
            <List.Item
              key={`${userIdOne}-${index}`}
              title={`${otherUserName} ${status === 'pending' ? ` - ${status}` : ''}`}
              description={id === user.currentDateId ? 'Current' : ''}
              descriptionStyle={[{ color: Colors.green500 }]}
              right={() => (
                <IconButton
                  icon="check-outline"
                  color={Colors.green500}
                  size={20}
                  onPress={() => handleMakeCurrent(id)}
                />
              )}
            />
          ))}
        </List.Section>
      </Surface>
    </ScrollView>
  );
}

export default withTheme(Dates);
