import React from 'react';

// Prop types.
import { themeTypes } from '../../../types';

// Components.
import { View } from 'react-native';
import { withTheme } from 'react-native-paper';

// Styles.
import styles from '../styles';

// Destruct styles.
const {
  loadingCardTitle, loadingCardSubtitle
} = styles;

function LoadingTitle({ theme }) {
  const { backdrop } = theme.colors;

  return (
    <View>
      <View style={[loadingCardTitle, { backgroundColor: backdrop }]} />
      <View style={[loadingCardSubtitle, { backgroundColor: backdrop }]} />
    </View>
  );
}

LoadingTitle.propTypes = {
  ...themeTypes.propTypes,
};

export default withTheme(LoadingTitle);
