// Constants.
import {
  DATE_REQUEST_TYPE,
  UNSEEN_STATUS
} from '../constants/socketIO';

export default class RatingHelper {
  static getUnseenNotificationIds(notifications) {
    return notifications
      .filter(({ type, status }) => {
        const isRequest = type === DATE_REQUEST_TYPE;
        const isUnseen = status === UNSEEN_STATUS;
        return isUnseen && !isRequest;
      })
      .map(({ id }) => id);
  }

  static getAllUnseenNotificationIds(notifications) {
    return notifications
      .filter(({ status }) => status === UNSEEN_STATUS)
      .map(({ id }) => id);

  }
}
