import React, { Component, Fragment } from 'react';
import { connect } from 'react-redux';

// Components.
import { Platform, View } from 'react-native';
import { HelperText, TextInput } from 'react-native-paper';
import CommonDialog from '../CommonDialog';

// Constants.
import {
  INVALID_PASSWORD,
  INVALID_CONFIRM_PASSWORD,
} from '../../modules/constants/authentication';

// Helpers.
import { ValidationHelper } from '../../modules/helpers';

// Services.
import { UserService } from '../../services';

// Styles.
import { utilityStyles } from '../../styles';

// Destruct helpers.
const { isPasswordInvalid } = ValidationHelper;

// Destruct styles.
const { textCenter, width100 } = utilityStyles;

class ChangePasswordDialog extends Component {
  constructor(props) {
    super(props);

    this.state = {
      currentPassword: {
        value: '',
        isDirty: false,
        isInvalid: false,
      },
      newPassword: {
        value: '',
        isDirty: false,
        isInvalid: false,
      },
      confirmPassword: {
        value: '',
        isDirty: false,
        isInvalid: false,
      },
      isPasswordsMatched: true,
    };

    // Bind class methods.
    this.handleOnChange = this.handleOnChange.bind(this);
    this.handleOnBlur = this.handleOnBlur.bind(this);
    this.handleOnSubmit = this.handleOnSubmit.bind(this);
  }

  handleOnChange(password, stateProperty) {
    const passwordData = this.state[stateProperty];
    const { isDirty } = passwordData;
    let { isInvalid } = passwordData;
    let { isPasswordsMatched } = this.state;

    if (isDirty) {
      isInvalid = isPasswordInvalid(password);

      if (stateProperty === 'confirmPassword') {
        isPasswordsMatched = password === this.state.newPassword.value;
        isInvalid = isInvalid || !isPasswordsMatched;
      }
    }

    this.setState(() => ({
      isPasswordsMatched,
      [stateProperty]: {
        ...passwordData,
        isInvalid,
        value: password,
      }
    }));
  }

  handleOnBlur(stateProperty) {
    const passwordData = this.state[stateProperty];
    const { value } = passwordData;
    let isInvalid = isPasswordInvalid(value);

    let { isPasswordsMatched } = this.state;
    if (stateProperty === 'confirmPassword') {
      isPasswordsMatched = value === this.state.newPassword.value;
      isInvalid = isInvalid || !isPasswordsMatched;
    }

    this.setState(() => ({
      isPasswordsMatched,
      [stateProperty]: {
        ...passwordData,
        isDirty: true,
        isInvalid,
      }
    }));
  }

  handleOnSubmit() {
    const {
      currentPassword: {
        value: currentValue,
        isInvalid: isCurrentInvalid,
      },
      newPassword: {
        isInvalid: isNewInvalid,
        value: newPassword
      },
      confirmPassword: { isInvalid: isConfirmInvalid },
    } = this.state;

    if (!isCurrentInvalid && !isNewInvalid && !isConfirmInvalid) {

      UserService.authenticateUser(currentValue.trim())
        .then(() => (
          UserService.updatePassword(newPassword.trim())
            .then(this.props.handleOnDismiss)
        ))
        .catch(error => alert(error.message));
    }
  }

  render() {
    const {
      isPasswordsMatched,
      currentPassword,
      newPassword,
      confirmPassword,
    } = this.state;

    const {
      value: currentValue,
      isInvalid: isCurrentInvalid,
    } = currentPassword;
    const {
      value: newValue,
      isInvalid: isNewInvalid,
    } = newPassword;
    const {
      value: confirmValue,
      isInvalid: isConfirmInvalid,
    } = confirmPassword;

    const { isShown, themeColor, handleOnDismiss } = this.props;

    const isSubmitDisabled =
      isCurrentInvalid || isNewInvalid || isConfirmInvalid
      || !currentValue || !newValue || !confirmValue;

    const inputs = [{
      label: 'Current Password',
      stateProperty: 'currentPassword',
      data: currentPassword,
    }, {
      label: 'New Password',
      stateProperty: 'newPassword',
      data: newPassword,
    }, {
      label: 'Confirm New Password',
      stateProperty: 'confirmPassword',
      data: confirmPassword,
    }];

    return (
      <CommonDialog
        isShown={isShown}
        titleText={'Change your password'}
        isSubmitDisabled={isSubmitDisabled}
        handleOnDismiss={handleOnDismiss}
        handleOnSubmit={this.handleOnSubmit}
        titleStyles={[textCenter]}
      >
        <Fragment>
          {inputs.map(({
            label,
            stateProperty,
            data: { value, isInvalid }
          }) => (
            <View key={`${stateProperty}`}>
              <TextInput
                label={label}
                value={value || ''}
                dense={true}
                autoFocus={true}
                mode="outlined"
                keyboardAppearance={themeColor}
                secureTextEntry={true}
                error={isInvalid}
                onBlur={() => this.handleOnBlur(stateProperty)}
                onChangeText={value => this.handleOnChange(value, stateProperty)}
                style={[width100]}
              />
              <HelperText
                type="error"
                visible={isInvalid}
                style={{ display: Platform.select({
                  native: isPasswordInvalid ? 'flex' : 'none',
                  default: 'flex'
                })}}
              >
                {stateProperty === 'confirmPassword' && !isPasswordsMatched
                  ? INVALID_CONFIRM_PASSWORD : INVALID_PASSWORD
                }
              </HelperText>
            </View>
          ))}
        </Fragment>
      </CommonDialog>
    );
  }
}


const mapStateToProps = state => ({
  themeColor: state.theme.themeColor
});

export default connect(
  mapStateToProps
)(ChangePasswordDialog);
