import React from 'react';

// Prop types.
import { ratingTypes } from '../../types';

// Components.
import { View } from 'react-native';
import { MaterialCommunityIcons } from '@expo/vector-icons';

// Styles.
import { utilityStyles } from '../../styles';

// Helpers.
import { RatingHelper } from '../../modules/helpers';

// Destruct styles.
const { flexRow, justifyContentCenter } = utilityStyles;

function Rating({ rating }) {
  // The color of the stars based on the rating.
  const ratingColor = RatingHelper.getRatingColor(rating);

  return (
    <View style={[flexRow, justifyContentCenter]}>
      {[1, 2, 3, 4, 5].map(value => (
        <MaterialCommunityIcons
          key={`star-rating-${value}`}
          name={RatingHelper.getStarIcon(rating, value)}
          color={ratingColor}
          size={36}
        />
      ))}
    </View>
  );
}

Rating.propTypes = ratingTypes.propTypes;

export default Rating;
