import { array, object, shape, string } from 'prop-types';

// Other prop types.
import navigationTypes from './navigation';
import themeTypes from './theme';

export default {
  propTypes: {
    currentRoute: string,
    descriptors: object,
    progress: object,
    state: object,
    routeNames: array,
    navigation: shape(navigationTypes.propTypes),
    theme: shape(themeTypes.propTypes),
  }
};
