import React, { Fragment } from 'react';
import { useSelector } from 'react-redux';

// Prop types.
import { placeCardTypes } from '../../types';

// Components.
import { Dimensions, View } from 'react-native';
import { Card, Colors, IconButton, Text } from 'react-native-paper';
import Rating from '../Rating';
import Pricing from '../Pricing';
import {
  LoadingTitle, LoadingContent, LoadingActions,
  StatusStamp
} from './subcomponents';

// Styles.
import styles from './styles';
import { utilityStyles } from '../../styles';

// Constants.
import {
  OPEN_TEXT, CLOSED_TEXT, LOADING_IMAGE
} from '../../modules/constants/place';
import {
  CHECK_OUTLINE,
  CLOSE_OUTLINE,
  BACK_ARROW_ICON,
} from '../../modules/constants/icons';

// Destruct styles.
const { cardCover, cardTitle, pricingText } = styles;
const {
  textCenter, flexRow, flex1, justifyContentAround, marginTopAuto,
  justifyContentCenter, alignItemsCenter, elevation12,
} = utilityStyles;

function PlaceCard({
  placeData, cardIndex, handleChooseOnPress,
  handleRejectPlaceOnPress, handleBackOnPress
}) {
  const {
    place_id,
    photoUrl,
    name,
    open_now,
    price_level,
    rating,
    vicinity,
  } = placeData;

  const isPlaceLoading = !place_id;

  const { viewedIds = [] } = useSelector(({ places }) => places);
  const { selectedPlaces = []} = useSelector(({ user }) => user);
  const selectedIds = selectedPlaces.map(({ place_id }) => place_id);

  const isViewed = viewedIds.includes(place_id);
  const isSelected = isViewed && selectedIds.includes(place_id);

  const imageSource = {
    uri: photoUrl,
    // height: 175,
  };

  const loadingImageSource = {
    ...imageSource,
    uri: LOADING_IMAGE,
  };

  const openText = open_now ? OPEN_TEXT : CLOSED_TEXT;

  const isBackButtonShown = cardIndex > 0;

  const { width, height } = Dimensions.get('window');

  alert(height)

  return (
    <Card style={[flex1, elevation12]}>
      {isViewed && <StatusStamp isSelected={isSelected} />}

      <Card.Cover
        source={imageSource}
        defaultSource={loadingImageSource}
        style={[cardCover]}
      />

      {isPlaceLoading ? (
        <LoadingTitle />
      ) : (
        <Card.Title
          title={name}
          subtitle={vicinity}
          titleNumberOfLines={2}
          titleStyle={[textCenter]}
          subtitleStyle={[textCenter]}
          style={[cardTitle]}
        />
      )}

      {width > 500 && (
        <Card.Content style={[flex1]}>
          {isPlaceLoading ? (
            <LoadingContent />
          ) : (
            <Fragment>
              {rating >= 0 && (
                <Rating rating={rating} />
              )}

              <View
                style={[flexRow, justifyContentAround, alignItemsCenter, { flexWrap: 'wrap' }]}
              >
                {price_level && (
                  <Pricing pricingLevel={price_level} />
                )}

                <Text style={[textCenter, pricingText]}>
                  {openText}
                </Text>
              </View>
            </Fragment>
          )}
        </Card.Content>
      )}

      <Card.Actions style={[justifyContentCenter, marginTopAuto]}>
        {isPlaceLoading ? (
          <LoadingActions />
        ) : (
          <Fragment>
            <View style={[{ flex: 1, margin: 6 }]}>
              {isBackButtonShown && (
                <IconButton
                  icon={BACK_ARROW_ICON}
                  color={Colors.blueGrey500}
                  size={48}
                  onPress={handleBackOnPress}
                />
              )}
            </View>

            <IconButton
              icon={CHECK_OUTLINE}
              color={Colors.green500}
              size={48}
              onPress={() => handleChooseOnPress(cardIndex)}
              style={[{ flex: 2 }]}
            />

            <IconButton
              icon={CLOSE_OUTLINE}
              color={Colors.red500}
              size={48}
              onPress={() => handleRejectPlaceOnPress(cardIndex)}
              style={[{ flex: 1 }]}
            />
          </Fragment>
        )}
      </Card.Actions>
    </Card>
  );
}

PlaceCard.defaultProps = placeCardTypes.defaultProps;
PlaceCard.propTypes = placeCardTypes.propTypes;

export default PlaceCard;
