// Constants.
import {
  UPDATE_LOCATION,
  UPDATE_SEARCH_RADIUS,
  UPDATE_NEXT_PAGE_TOKEN,
} from '../modules/constants/actions';

export default function(state = {}, action) {
  const { payload, type } = action;
  let updatedState = { ...state };

  switch (type) {
    case UPDATE_LOCATION:
      updatedState = {
        ...state,
        coords: payload,
      };
      break;
    case UPDATE_SEARCH_RADIUS:
      updatedState = {
        ...state,
        searchRadius: payload,
      };
      break;
    case UPDATE_NEXT_PAGE_TOKEN:
      updatedState = {
        ...state,
        nextPageToken: payload,
      };
      break;
    default:
      updatedState = { ...state };
  }

  return updatedState;
}
