import {
  LOGIN,
  LOG_OUT,
  SIGN_UP,
  STORE_DISPLAY_NAME,
  STORE_EMAIL,
  STORE_PASSWORD,
} from '../modules/constants/actions';

export default function(state = {}, action) {
  const { payload, type } = action;
  let updatedState = { ...state };
  switch (type) {
    case LOGIN:
      updatedState = payload;
      break;
    case LOG_OUT:
      updatedState = payload;
      break;
    case SIGN_UP:
      updatedState = payload;
      break;
    case STORE_DISPLAY_NAME:
      updatedState = {
        ...state,
        displayName: payload,
      };
      break;
    case STORE_EMAIL:
      updatedState = {
        ...state,
        email: payload,
      };
      break;
    case STORE_PASSWORD:
      updatedState = {
        ...state,
        password: payload,
      };
      break;
    default:
      updatedState = { ...state };
  }

  return updatedState;
}
