import React from 'react';
import { useDispatch } from 'react-redux';

// Prop types.
import { drawerContentTypes } from '../../types';

// Components.
import { View } from 'react-native';
import { Divider, withTheme } from 'react-native-paper';
import { DrawerContentScrollView } from '@react-navigation/drawer';
import { DateRequestDialog, InfoSection, ThemeSwitch } from '../../components';
import { NavigationSection, LogOutSection } from './subcomponents';

// Styles.
import styles from './styles';

// Services.
import { logOut } from '../../actions/user';

// Destruct styles.
const { content } = styles;

function DrawerContent(props) {
  const dispatch = useDispatch();

  const {
    currentRoute,
    navigation,
    theme: { colors }
  } = props;

  const handleNavItemOnPress = screen => {
    navigation.navigate(screen);
  };

  const handleLogOutOnPress = () => {
    navigation.closeDrawer();
    dispatch(logOut());
  };

  return (
    <DrawerContentScrollView {...props} style={[{ backgroundColor: colors.surface, borderRightWidth: 2, borderRightColor: colors.primary }]}>
      <View style={[content]}>
        <InfoSection />

        <Divider />

        <DateRequestDialog />

        <NavigationSection
          currentRoute={currentRoute}
          handleNavItemOnPress={handleNavItemOnPress}
        />

        <ThemeSwitch />

        <LogOutSection handleLogOutOnPress={handleLogOutOnPress} />
      </View>
    </DrawerContentScrollView>
  );
}

DrawerContent.propTypes = drawerContentTypes.propTypes;

export default withTheme(DrawerContent);
