// Constants.
import {
  USERS_API_ROUTE,
  REQUEST_HEADERS,
} from '../modules/constants/api';

// Services.
import UserService from './UserService';

export default class DateService {
  static async getSelectedPlaces(userId, dateId) {
    const idToken = await UserService.getIdToken();

    return fetch(`${USERS_API_ROUTE}/${userId}/dates/${dateId}/places`, {
      method: 'GET',
      headers: {
        ...REQUEST_HEADERS,
        Authorization: `Basic ${idToken}`
      }
    }).then(response => response.json());
  }

  static async updateSelectedPlaces(userId, dateId, place) {
    const idToken = await UserService.getIdToken();

    return fetch(`${USERS_API_ROUTE}/${userId}/dates/${dateId}/places`, {
      method: 'PUT',
      headers: {
        ...REQUEST_HEADERS,
        Authorization: `Basic ${idToken}`
      },
      body: JSON.stringify({ place })
    });
  }

  static async removeSelectedPlace(userId, dateId, placeId) {
    const idToken = await UserService.getIdToken();

    return fetch(`${USERS_API_ROUTE}/${userId}/dates/${dateId}/places/${placeId}`, {
      method: 'DELETE',
      headers: {
        ...REQUEST_HEADERS,
        Authorization: `Basic ${idToken}`
      },
    });
  }
}
