// Services.
import { UserService } from '../services';

// Constants.
import {
  STORE_DISPLAY_NAME,
  STORE_EMAIL,
  STORE_PASSWORD,
  LOGIN,
  LOG_OUT,
  SIGN_UP,
} from '../modules/constants/actions';

/**
 * Define Actions.
*/

// Store Display Name.
export const storeDisplayName = displayName => {
  return {
    type: STORE_DISPLAY_NAME,
    payload: displayName,
  };
};

// Store Email.
export const storeEmail = email => {
  return {
    type: STORE_EMAIL,
    payload: email,
  };
};

// Store Password.
export const storePassword = password => {
  return {
    type: STORE_PASSWORD,
    payload: password,
  };
};

// Login.
export const login = () => {
  return async (dispatch, getState) => {
    try {
      const { email, password } = getState().user;

      await UserService.signInEmail(email.trim(), password.trim());

      dispatch(getUser());
    } catch (error) {
      alert(error);
    }
  };
};

// Log Out.
export const logOut = () => {
  return async dispatch => {
    try {
      await UserService.logOut();

      dispatch({
        type: LOG_OUT,
        payload: {}
      });
    } catch (error) {
      alert(error);
    }
  };
};

// Sign Up.
export const signUp = () => {
  return async (dispatch, getState) => {
    try {
      const { displayName, email, password } = getState().user;
      const trimmedEmail = email.trim();

      const uid = await UserService.signUpEmail(trimmedEmail, password.trim());

      if (uid) {
        const user = {
          uid,
          displayName: displayName.trim(),
          email: trimmedEmail,
        };

        await UserService.updateProfile(user.displayName, 'displayName');

        dispatch({
          type: SIGN_UP,
          payload: user,
        });
      }
    } catch (error) {
      alert(error);
    }
  };
};

// Get User.
export const getUser = () => {
  return async dispatch => {
    try {
      const user = await UserService.getUser();

      dispatch({
        type: LOGIN,
        payload: user,
      });
    } catch (error) {
      alert(error);
    }
  };
};
