import React from 'react';
import {
  createMaterialTopTabNavigator,
} from '@react-navigation/material-top-tabs';

// Components.
import { Dimensions } from 'react-native';
import { Home, Dates } from '../../views';

// Constants.
import {
  HOME_ROUTE, DATES_ROUTE,
} from '../../modules/constants/authentication';

// Create the tab navigator.
const Tab = createMaterialTopTabNavigator();

const { width } = Dimensions.get('window');

const screens = [{
  routeName: HOME_ROUTE,
  View: Home,
}, {
  routeName: DATES_ROUTE,
  View: Dates,
}];

export default function TabNavigator() {
  return (
    <Tab.Navigator
      initialRouteName={HOME_ROUTE}
      swipeEnabled={false}
      style={[{ width }]}
      sceneContainerStyle={[{ width }]}
    >
      {screens.map(({ routeName, View }) => (
        <Tab.Screen key={routeName} name={routeName}>
          {props => <View { ...props } />}
        </Tab.Screen>
      ))}
    </Tab.Navigator>
  );
}
