import React from 'react';

// Prop types.
import { statusStampTypes } from '../../../types';

// Components.
import { View } from 'react-native';
import { Colors, Text } from 'react-native-paper';

// Constants.
import {
  CHOSEN_PLACE_TEXT,
  REJECTED_PLACE_TEXT,
  CHOSEN_PLACE_BG_COLOR,
  REJECT_PLACE_BG_COLOR,
} from '../../../modules/constants/place';

// Styles.
import styles from '../styles';

// Destruct styles.
const { statusContainer, statusText } = styles;

function StatusStamp({ isSelected }) {
  const viewedText = isSelected ? CHOSEN_PLACE_TEXT : REJECTED_PLACE_TEXT;

  // Dynamic styles.
  const borderColor = isSelected ? Colors.green700 : Colors.red700;
  const bgColor = isSelected ? CHOSEN_PLACE_BG_COLOR : REJECT_PLACE_BG_COLOR;
  const textColor = isSelected ? Colors.green700 : Colors.red700;

  return (
    <View style={[statusContainer, {
      borderColor: borderColor,
      backgroundColor: bgColor,
    }]}>
      <Text style={[statusText, { color: textColor }]}>
        {viewedText}
      </Text>
    </View>
  );
}

StatusStamp.propTypes = statusStampTypes.propTypes;

export default StatusStamp;
