import React from 'react';

// Components
import { Linking, Platform, View } from 'react-native';
import { Button, Title } from 'react-native-paper';

// Styles.
import { utilityStyles } from '../../styles';

// Destruct styles.
const { textCenter } = utilityStyles;

export default function MatchContent({ place }) {
  const {
    name,
    vicinity,
    phoneNumber
  } = place;

  const handleCall = Platform.select({
    web: () => {
      window.open(`tel:${phoneNumber}`);
    },
    default: () => {
      Linking.openURL(`tel:${phoneNumber}`);
    }
  });

  const handleGetDirections = Platform.select({
    ios: () => {
      Linking.openURL(`http://maps.apple.com/maps?daddr=${vicinity}`);
    },
    android: () => {
      Linking.openURL(`http://maps.google.com/maps?daddr=${vicinity}`);
    },
    web: () => {
      window.open(`http://maps.google.com/maps?daddr=${vicinity}`, '_blank');
    }
  });

  return (
    <View style={[textCenter]}>
      <Title style={[textCenter]}>
        {name}
      </Title>

      <View>
        <Button
          icon="phone"
          onPress={handleCall}
        >
          {phoneNumber}
        </Button>
        <Button
          icon="map-marker"
          onPress={handleGetDirections}
        >
          {vicinity}
        </Button>
      </View>
    </View>
  );
}
