import { STORE_SOCKET_IO } from '../modules/constants/actions';

export default function(state = {}, action) {
  const { payload } = action;
  let updatedState = state;

  switch (action.type) {
    case STORE_SOCKET_IO:
      updatedState = payload;
      break;
    default:
      updatedState = state;
  }

  return updatedState;
}
