// Constants.
import {
  UPDATE_PLACES,
  ADD_SEEN_PLACE_ID,
  UPDATE_VIEWED_IDS,
  STORE_SELECTED_PLACE
} from '../modules/constants/actions';

// Services.
import { PlaceService } from '../services';

// Update All Places.
export const updatePlaces = places => {
  return {
    type: UPDATE_PLACES,
    payload: places,
  };
};

// Add Viewed Place Id
export const addViewedPlaceId = placeId => {
  return (dispatch, getState) => {
    const { viewedIds = [] } = getState().places;

    viewedIds.push(placeId);

    dispatch({
      type: ADD_SEEN_PLACE_ID,
      payload: viewedIds
    });
  };
};

// Update Viewed Ids.
export const updateViewedIds = viewedIds => {
  return {
    type: UPDATE_VIEWED_IDS,
    payload: viewedIds,
  };
};

// Store Selected Place.
export const storeSelectedPlace = (userId, dateId, place) => {
  return async (dispatch, getState) => {
    let { selected: selectedPlaces = [] } = getState().places;

    selectedPlaces = selectedPlaces.map(placeData => {
      const updatedPlace = { ...placeData };

      if (placeData.dateId === dateId) {
        updatedPlace.selectedPlaces.push(place);
      }

      return updatedPlace;
    });

    await PlaceService.updateSelectedPlaces(userId, dateId, place);

    dispatch({
      type: STORE_SELECTED_PLACE,
      payload: selectedPlaces
    });
  };
};

// Remove Selected Place.
export const removeSelectedPlace = (userId, placeId, dateId) => {
  return async (dispatch, getState) => {
    let { selected: selectedPlaces = [] } = getState().places;

    selectedPlaces = selectedPlaces.map(placeData => {
      const updatedPlace = { ...placeData };

      if (placeData.dateId === dateId) {
        updatedPlace.selectedPlaces = updatedPlace
          .selectedPlaces.filter(({ place_id }) => place_id !== placeId);
      }

      return updatedPlace;
    });

    await PlaceService.removeSelectedPlace(userId, dateId, placeId);

    dispatch({
      type: STORE_SELECTED_PLACE,
      payload: selectedPlaces
    });
  };
};

// Get Selected Places.
export const getSelectedPlaces = (userId, dateId) => {
  return async dispatch => {
    const selectedPlaces = await PlaceService.getSelectedPlaces(userId, dateId);

    dispatch({
      type: STORE_SELECTED_PLACE,
      payload: selectedPlaces,
    });
  };
};
