import React from 'react';

// Components.
import { View } from 'react-native';
import { List, withTheme } from 'react-native-paper';
import Title from './Title';
import Actions from './Actions';
import Submenu from './Submenu';

// Constants.
import {
  DATE_REQUEST_TYPE,
  UNSEEN_STATUS,
} from '../../../modules/constants/socketIO';

// Styles.
import { utilityStyles } from '../../../styles';

// Destruct styles.
const {
  fontBold, paddingVertical0, paddingLeft0,
  marginBottom0,
} = utilityStyles;

function Item({
  notification, theme, handleAccept,
  handleDecline,
}) {
  const {
    id, message, type, status, senderName, timestamp,
  } = notification;

  const { colors } = theme;

  const isRequest = type === DATE_REQUEST_TYPE;
  const isUnseen = status === UNSEEN_STATUS;

  const backgroundColor = isUnseen ? colors.backdrop : '';

  const handleAcceptOnPress = () => handleAccept(notification);
  const handleDeclineOnPress = () => handleDecline(notification);

  return (
    <View style={[{ backgroundColor }]}>
      <List.Item
        title={(
          <Title senderName={senderName} timestamp={timestamp} />
        )}
        description={message}
        right={() => <Submenu notificationId={id} />}
        titleStyle={[fontBold]}
        style={[paddingVertical0, paddingLeft0, marginBottom0]}
      />

      {isRequest && isUnseen && (
        <Actions
          handleAcceptOnPress={handleAcceptOnPress}
          handleDeclineOnPress={handleDeclineOnPress}
        />
      )}
    </View>
  );
}

export default withTheme(Item);
